<template>
  <el-dialog
    title="Add Truck"
    v-model="dialogVisible"
    width="600px"
    @close="closeDialog"
    append-to-body
    :lock-scroll="false"
    :modal-append-to-body="false"
  >
    <el-form ref="formRef" :model="formData" label-width="200px">
      <!-- Truck Group ID (自動填入) -->
      <el-form-item label="Truck Group ID">
        <el-input v-model="formData.truck_group_id" disabled />
      </el-form-item>

      <!-- Truck Master ID -->
      <el-form-item label="Truck Master ID">
        <el-input v-model="formData.truck_master_id" />
      </el-form-item>

      <!-- Truck Area -->
      <el-form-item label="Truck Area">
        <el-input v-model="formData.truck_area" />
      </el-form-item>

      <!-- Truck Corp -->
      <el-form-item label="Truck Corp">
        <el-input v-model="formData.truck_corp" />
      </el-form-item>

      <!-- Truck Year -->
      <el-form-item label="Truck Year">
        <el-input v-model="formData.truck_year" />
      </el-form-item>

      <!-- Truck Make -->
      <el-form-item label="Truck Make">
        <el-input v-model="formData.truck_make" />
      </el-form-item>

      <!-- Truck Plate -->
      <el-form-item label="Truck Plate">
        <el-input v-model="formData.truck_plate" />
      </el-form-item>

      <!-- Vin Number -->
      <el-form-item label="Vin Number">
        <el-input v-model="formData.vin_number" />
      </el-form-item>

      <!-- Truck State -->
      <el-form-item label="Truck State">
        <el-select v-model="formData.truck_state" placeholder="Select State">
          <el-option label="ACTIVE" :value="1" />
          <el-option label="INACTIVE" :value="0" />
        </el-select>
      </el-form-item>

      <!-- Truck Ins Expired Time (日期選擇器) -->
      <el-form-item label="Truck Ins Expired Time">
        <el-date-picker
          v-model="formData.truck_ins_expired_at_time"
          type="date"
          format="MM-DD-YYYY"
          value-format="YYYY-MM-DDTHH:mm:ss.SSS"
        />
      </el-form-item>

      <!-- Truck Reg Expired Time (日期選擇器) -->
      <el-form-item label="Truck Reg Expired Time">
        <el-date-picker
          v-model="formData.truck_reg_expired_at_time"
          type="date"
          format="MM-DD-YYYY"
          value-format="YYYY-MM-DDTHH:mm:ss.SSS"
        />
      </el-form-item>
    </el-form>

    <!-- Dialog Footer -->
    <template #footer>
      <el-button @click="closeDialog">Close</el-button>
      <el-button type="primary" @click="submitAdd" :loading="loading"
        >Save</el-button
      >
    </template>
  </el-dialog>
</template>

<script>
import axios from "axios";
import { ref, computed, watch, onMounted } from "vue";
import { ElMessage } from "element-plus";
import { API_BASE_URL } from "@/config"; // ✅ 這樣才會正確導入變數

export default {
  props: {
    visible: Boolean, // 控制對話框開關
    truckList: Array, // Truck 列表，來自 TruckPage，應該包含所有頁數的數據
  },
  setup(props, { emit }) {
    const dialogVisible = computed({
      get: () => props.visible,
      set: (val) => {
        if (!val) closeDialog();
      },
    });

    const loading = ref(false);

    const formData = ref({
      truck_group_id: "", // 自動填入
      truck_master_id: "",
      truck_area: "",
      truck_corp: "",
      truck_year: "",
      truck_make: "",
      truck_plate: "",
      vin_number: "",
      truck_state: 1, // 預設 ACTIVE
      truck_ins_expired_at_time: "",
      truck_reg_expired_at_time: "",
    });

    /** 🔄 取得最新 `Truck Group ID` */
    const updateTruckGroupId = () => {
      if (props.truckList && props.truckList.length > 0) {
        // 過濾掉 `N/A` 或 `null`，確保 truck_group_id 皆為數字
        const validGroupIds = props.truckList
          .map((truck) => Number(truck.truck_group_id))
          .filter((id) => !isNaN(id) && id > 0);

        // 計算最大 truck_group_id +1
        const maxTruckGroupId =
          validGroupIds.length > 0 ? Math.max(...validGroupIds) : 0;
        formData.value.truck_group_id = String(maxTruckGroupId + 1);
      } else {
        formData.value.truck_group_id = String(1); // 若無資料，從 1 開始
      }
    };

    /** 🚀 監聽 `truckList` 變化並更新 `Truck Group ID` */
    watch(() => props.truckList, updateTruckGroupId, { immediate: true });

    /** 🚀 當組件掛載時確保 Truck Group ID 最新 */
    onMounted(() => {
      updateTruckGroupId();
    });

    /** 🚀 提交新增 Truck */
    const submitAdd = async () => {
      if (
        !formData.value.truck_group_id ||
        !formData.value.truck_area ||
        !formData.value.truck_corp
      ) {
        ElMessage.error("Truck Group ID、Truck Area、Truck Corp 為必填！");
        return;
      }

      loading.value = true;

      try {
        const token = localStorage.getItem("authToken");

        const payload = {
          Token: token,
          Timestamp: Date.now(),
          Sign: "string",
          truck_data: {
            ...formData.value,
            // 確保 truck_state 傳遞時為 1 或 0
            truck_state: formData.value.truck_state === 1 ? 1 : 0,
          },
        };

        console.log("🚀 API 傳遞的資料:", payload);

        const response = await axios.post(
          `${API_BASE_URL}/Truck/TruckAdd`,
          payload
        );

        if (response.data.Code === 1000) {
          ElMessage.success("Truck added successfully!");
          emit("add-success");
          closeDialog();
        } else {
          ElMessage.error("Add failed: " + response.data.Code);
        }
      } catch (error) {
        console.error("API error:", error);
        ElMessage.error("API request failed.");
      } finally {
        loading.value = false;
      }
    };

    /** 🚀 關閉對話框 */
    const closeDialog = () => {
      emit("close");
    };

    return {
      dialogVisible,
      formData,
      loading,
      submitAdd,
      closeDialog,
    };
  },
};
</script>

<style scoped>
.el-dialog {
  border-radius: 8px;
}
</style>
