          <span :class="{ 'collapsed-text': collapsed }">{{ item.name }}</span>
        </router-link>
      </li>
    </ul>
    <button class="toggle-button" @click="toggleSidebar">
      {{ collapsed ? "▶" : "◀" }}
    </button>
  </div>
</template>

<script>
import { mapGetters } from "vuex";

export default {
  props: {
    collapsed: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      menuItems: [
        { name: "Order", route: "/order" },
        { name: "Customer", route: "/customer" },
        { name: "Chassis", route: "/chassis" },
        { name: "Truck", route: "/truck" },
        { name: "Driver Fleet", route: "/driver-fleet" },
        { name: "Port", route: "/port" },
        { name: "Truck Driver", route: "/truck-driver" },
        { name: "Map", route: "/map" },
        { name: "Admin", route: "/admin" },
        { name: "Rank", route: "/rank" },
        { name: "Billing", route: "/billing" },
      ],
    };
  },
  computed: {
    ...mapGetters(["isAuthenticated"]),
  },
  methods: {
    toggleSidebar() {
      this.$emit("toggle-sidebar");
    },
  },
};
</script>

