<template>
  <el-dialog
    title="Location"
    v-model="dialogVisible"
    width="80%"
    @open="fetchLocations"
    @close="closeDialog"
    :lock-scroll="false"
    :modal-append-to-body="false"
  >
    <div class="dialog-container">
      <!-- 🔵 新增 Location 按鈕 -->
      <div class="button-container">
        <el-button type="primary" size="small" @click="openAddLocationDialog">
          Add Location
        </el-button>
      </div>

      <!-- 📌 資料表格 -->
      <el-table :data="locationList" border stripe>
        <el-table-column
          prop="customer_location"
          label="CUSTOMER / Location"
          width="200"
        />
        <el-table-column
          prop="customer_address"
          label="ADDRESS DETAIL"
          width="300"
        />
        <el-table-column prop="Void_Price" label="Void Price" width="120" />
        <el-table-column prop="SPLIT" label="Split" width="100" />
        <el-table-column prop="TRIAXLE" label="Triaxle" width="100" />
        <el-table-column prop="PP_CTF" label="PP CTF" width="100" />
        <el-table-column prop="LAYOVER" label="Layover" width="100" />
        <el-table-column prop="SCALE_TICKET" label="Scale Ticket" width="120" />
        <el-table-column
          prop="SCALE_TICKET_HAZMAT"
          label="Scale Ticket Hazmat"
          width="150"
        />
      </el-table>
    </div>

    <!-- ❌ 關閉按鈕 -->
    <template #footer>
      <el-button @click="closeDialog">Close</el-button>
    </template>

    <!-- ➕ 新增 Location Dialog -->
    <CustomerLocationAddDialog
      v-if="addLocationDialogVisible"
      :visible="addLocationDialogVisible"
      :customerList="customerList"
      @close="addLocationDialogVisible = false"
      @add-success="fetchLocations"
    />
  </el-dialog>
</template>

<script>
import axios from "axios";
import { ref, computed, watchEffect } from "vue";
import CustomerLocationAddDialog from "@/components/CustomerLocationAddDialog.vue";
import { API_BASE_URL } from "@/config"; // ✅ 這樣才會正確導入變數

export default {
  props: {
    visible: Boolean, // `v-model:visible`
    customerId: Number, // 客戶 ID
  },
  emits: ["update:visible"], // 讓父元件控制開關

  components: { CustomerLocationAddDialog },

  setup(props, { emit }) {
    // ✅ `computed` 來處理 v-model 綁定
    const dialogVisible = computed({
      get: () => props.visible,
      set: (val) => {
        if (!val) closeDialog();
      },
    });

    const locationList = ref([]);
    const addLocationDialogVisible = ref(false); // 控制 Add Location 對話框

    /** 📌 取得 Location 清單 */
    const fetchLocations = async () => {
      if (!props.customerId) {
        console.warn("❌ customerId 未提供，無法請求 API");
        return;
      }

      console.log("📡 Fetching locations for customerId:", props.customerId);
      try {
        const token = localStorage.getItem("authToken");
        if (!token) {
          alert("Session expired, please log in again.");
          return;
        }

        const response = await axios.post(
          `${API_BASE_URL}/Customer/Location`,
          {
            Token: token,
            Timestamp: Date.now(),
            Sign: "string",
            customer_id: props.customerId,
          }
        );

        if (response.data.Code === 1000) {
          locationList.value = response.data.location_list || [];
        } else {
          console.error("⚠️ API Error:", response.data);
          locationList.value = [];
        }
      } catch (error) {
        console.error("❌ API Request Failed:", error);
      }
    };

    // 🔥 當 `customerId` 變更時，立即觸發 API
    watchEffect(() => {
      if (props.visible) {
        fetchLocations();
      }
    });

    /** 📌 開啟新增 Location 對話框 */
    const openAddLocationDialog = () => {
      addLocationDialogVisible.value = true;
    };

    /** ✅ 新增成功後，重新獲取資料 */
    const handleAddSuccess = () => {
      addLocationDialogVisible.value = false;
      fetchLocations();
    };

    /** 📌 關閉對話框 */
    const closeDialog = () => {
      emit("update:visible", false);
    };

    return {
      dialogVisible,
      locationList,
      fetchLocations,
      addLocationDialogVisible,
      openAddLocationDialog,
      handleAddSuccess,
      closeDialog,
    };
  },
};
</script>

<style scoped>
.dialog-container {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.button-container {
  margin-bottom: 10px;
}
</style>
