import { createStore } from "vuex"; // ✅ 確保有正確導入 Vuex

export default createStore({
  state: {
    authToken: localStorage.getItem("authToken") || "",
    username: localStorage.getItem("username") || "Guest",
    groups: JSON.parse(localStorage.getItem("groups")) || [],
    tokenExpiration: localStorage.getItem("tokenExpiration") || null,
  },
  mutations: {
    SET_AUTH_TOKEN(state, token) {
      state.authToken = token;
      localStorage.setItem("authToken", token);
    },
    SET_USERNAME(state, username) {
      state.username = username;
      localStorage.setItem("username", username);
    },
    SET_GROUPS(state, groups) {
      state.groups = groups;
      localStorage.setItem("groups", JSON.stringify(groups));
    },
    SET_TOKEN_EXPIRATION(state, expiration) {
      state.tokenExpiration = expiration;
      localStorage.setItem("tokenExpiration", expiration);
    },
    LOGOUT(state) {
      state.authToken = "";
      state.username = "Guest";
      state.groups = [];
      state.tokenExpiration = null;
      localStorage.removeItem("authToken");
      localStorage.removeItem("username");
      localStorage.removeItem("groups");
      localStorage.removeItem("tokenExpiration");
    },
  },
  actions: {
    login({ commit }, { token, username, groups }) {
      const expirationTime = Date.now() + 24 * 60 * 60 * 1000; // 設定1天後過期
      commit("SET_AUTH_TOKEN", token);
      commit("SET_USERNAME", username);
      commit("SET_GROUPS", groups || []);
      commit("SET_TOKEN_EXPIRATION", expirationTime);
    },
    logout({ commit }) {
      commit("LOGOUT");
    },
    checkTokenExpiration({ commit, state }) {
      if (state.tokenExpiration && Date.now() > state.tokenExpiration) {
        commit("LOGOUT");
        alert("Session expired. Please log in again.");
      }
    },
  },
  getters: {
    isAuthenticated: (state) => !!state.authToken,
    username: (state) => state.username,
    userGroups: (state) => state.groups,
  },
});
