<template>
  <el-dialog
    title="Add Location"
    v-model="dialogVisible"
    width="600px"
    @close="closeDialog"
    :lock-scroll="false"
    :modal-append-to-body="false"
  >
    <div class="dialog-container">
      <el-form ref="formRef" :model="formData" label-width="180px">
        <el-form-item label="Customer Location" required>
          <el-input
            v-model="formData.customer_location"
            placeholder="Enter Location"
          />
        </el-form-item>

        <el-form-item label="Address" required>
          <el-input
            v-model="formData.customer_address"
            placeholder="Enter Address"
          />
        </el-form-item>

        <div class="scrollable-content">
          <el-row :gutter="20">
            <el-col
              :span="12"
              v-for="(field, label) in numberFields"
              :key="label"
            >
              <el-form-item :label="label">
                <el-input-number v-model="formData[field]" :min="0" />
              </el-form-item>
            </el-col>
          </el-row>
        </div>
      </el-form>
    </div>

    <template #footer>
      <el-button @click="closeDialog">Cancel</el-button>
      <el-button type="primary" @click="submitAdd">Save</el-button>
    </template>
  </el-dialog>
</template>

<script>
import { ref, computed, watch } from "vue";
import { ElMessage } from "element-plus";
import axios from "axios";
import { API_BASE_URL } from "@/config"; // ✅ 這樣才會正確導入變數

export default {
  props: {
    visible: Boolean,
    customerList: {
      type: Array,
      default: () => [],
    },
  },
  emits: ["close", "add-success"],

  setup(props, { emit }) {
    const dialogVisible = computed({
      get: () => props.visible,
      set: (val) => {
        if (!val) closeDialog();
      },
    });

    const formData = ref({
      customer_id: 0,
      customer_location: "",
      customer_address: "",
      customer_rate: 0,
      driver_rate: 0,
      FUEL_SURCHARGE: 0,
      HAZ: 0,
      HAZ_BATTERIES: 0,
      INSPECTION: 0,
      DROP_FEE: 0,
      CHASSIS: 0,
      STORAGE: 0,
      CHASSIS_STORAGE: 0,
      REEFER: 0,
      OVERWEIGHT: 0,
      PREPULL: 0,
      BOLT_SEAL: 0,
      EXAM_SITE_EXTRA: 0,
      Void_Price: 0,
      SPLIT: 0,
      TRIAXLE: 0,
      PP_CTF: 0,
      LAYOVER: 0,
      SCALE_TICKET: 0,
      SCALE_TICKET_HAZMAT: 0,
    });

    const numberFields = ref({
      "Customer Rate": "customer_rate",
      "Driver Rate": "driver_rate",
      "Fuel Surcharge": "FUEL_SURCHARGE",
      Hazmat: "HAZ",
      "Haz Batteries": "HAZ_BATTERIES",
      Inspection: "INSPECTION",
      "Drop Fee": "DROP_FEE",
      Chassis: "CHASSIS",
      Storage: "STORAGE",
      "Chassis + Storage": "CHASSIS_STORAGE",
      Reefer: "REEFER",
      Overweight: "OVERWEIGHT",
      Prepull: "PREPULL",
      "Bolt Seal": "BOLT_SEAL",
      "Exam Site Extra": "EXAM_SITE_EXTRA",
      "Void Price": "Void_Price",
      Split: "SPLIT",
      Triaxle: "TRIAXLE",
      "PP CTF": "PP_CTF",
      Layover: "LAYOVER",
      "Scale Ticket": "SCALE_TICKET",
      "Scale Ticket Hazmat": "SCALE_TICKET_HAZMAT",
    });

    const setCustomerId = () => {
      if (Array.isArray(props.customerList) && props.customerList.length > 0) {
        const maxId = Math.max(
          ...props.customerList.map((c) => c.customer_id || 0)
        );
        formData.value.customer_id = maxId + 1;
      } else {
        formData.value.customer_id = 1;
      }
      console.log("✅ 設定 customer_id:", formData.value.customer_id);
    };

    watch(
      () => props.visible,
      (newVal) => {
        if (newVal) {
          setCustomerId();
        }
      }
    );

    const submitAdd = async () => {
      if (
        !formData.value.customer_location ||
        !formData.value.customer_address
      ) {
        ElMessage.error("Location Name and Address are required!");
        return;
      }

      try {
        const token = localStorage.getItem("authToken");
        const payload = {
          Token: token,
          Timestamp: Date.now(),
          Sign: "string",
          location: { ...formData.value },
        };

        console.log("🚀 送出 API 資料:", payload);

        const response = await axios.post(
          `${API_BASE_URL}/Customer/LocationAdd`,
          payload
        );

        if (response.data.Code === 1000) {
          ElMessage.success("Location added successfully!");
          emit("add-success");
          closeDialog();
        } else {
          ElMessage.error("Add failed: " + response.data.Code);
        }
      } catch (error) {
        console.error("❌ API error:", error);
        ElMessage.error("API request failed.");
      }
    };

    const closeDialog = () => {
      emit("close");
    };

    return {
      dialogVisible,
      formData,
      numberFields,
      submitAdd,
      closeDialog,
    };
  },
};
</script>
