<template>
  <el-dialog
    title="Add Chassis"
    v-model="dialogVisible"
    width="600px"
    @close="closeDialog"
    append-to-body
    :lock-scroll="false"
    :modal-append-to-body="false"
  >
    <el-form ref="formRef" :model="formData" label-width="180px">
      <!-- Chassis NO -->
      <el-form-item label="Chassis NO">
        <el-input
          v-model="formData.trailer_no"
          placeholder="Enter Chassis NO"
        />
      </el-form-item>

      <!-- Chassis Plate -->
      <el-form-item label="Chassis Plate">
        <el-input
          v-model="formData.trailer_plate"
          placeholder="Enter Chassis Plate"
        />
      </el-form-item>

      <!-- Chassis Reg Expired At -->
      <el-form-item label="Chassis Reg Expired At">
        <el-date-picker
          v-model="formData.trailer_reg_expired_at"
          type="date"
          format="MM-DD-YYYY"
          value-format="YYYY-MM-DD"
          placeholder="Select Date"
        />
      </el-form-item>

      <!-- Chassis Plate Expired At -->
      <el-form-item label="Chassis Plate Expired At">
        <el-date-picker
          v-model="formData.trailer_plate_expired_at"
          type="date"
          format="MM-DD-YYYY"
          value-format="YYYY-MM-DD"
          placeholder="Select Date"
        />
      </el-form-item>

      <!-- Chassis Check Expired At -->
      <el-form-item label="Chassis Check Expired At">
        <el-date-picker
          v-model="formData.trailer_check_expired_at"
          type="date"
          format="MM-DD-YYYY"
          value-format="YYYY-MM-DD"
          placeholder="Select Date"
        />
      </el-form-item>

      <!-- Chassis Corp -->
      <el-form-item label="Chassis Corp">
        <el-select
          v-model="formData.trailer_corp"
          placeholder="Select Chassis Corp"
        >
          <el-option
            v-for="corp in chassisCorps"
            :key="corp"
            :label="corp"
            :value="corp"
          />
        </el-select>
      </el-form-item>

      <!-- Chassis Area -->
      <el-form-item label="Chassis Area">
        <el-input
          v-model="formData.trailer_area"
          placeholder="Enter Chassis Area"
        />
      </el-form-item>
    </el-form>

    <!-- Dialog Footer -->
    <template #footer>
      <el-button @click="closeDialog">Close</el-button>
      <el-button type="primary" @click="submitAdd" :loading="loading"
        >Save</el-button
      >
    </template>
  </el-dialog>
</template>

<script>
import axios from "axios";
import { ref, computed, watch } from "vue";
import { ElMessage } from "element-plus";
import { API_BASE_URL } from "@/config"; // ✅ 這樣才會正確導入變數

export default {
  props: {
    visible: Boolean, // 控制對話框開關
  },
  emits: ["update:visible", "add-success"], // 修正 emit 事件
  setup(props, { emit }) {
    const dialogVisible = computed({
      get: () => props.visible,
      set: (val) => {
        if (!val) closeDialog();
      },
    });

    const loading = ref(false);

    const defaultFormData = () => ({
      trailer_no: "",
      trailer_plate: "",
      trailer_reg_expired_at: "",
      trailer_plate_expired_at: "",
      trailer_check_expired_at: "",
      trailer_corp: "",
      trailer_area: "",
    });

    const formData = ref(defaultFormData());

    const chassisCorps = ref(["TREND", "TRAC", "PORT", "TOPPOINT", "4JOHN"]);

    /** 🚀 監聽 `dialogVisible` 變化，關閉時清空表單 */
    watch(dialogVisible, (newVal) => {
      if (!newVal) {
        formData.value = defaultFormData();
      }
    });

    /** 🚀 提交新增 Chassis */
    const submitAdd = async () => {
      if (!formData.value.trailer_no || !formData.value.trailer_plate) {
        ElMessage.error("Chassis NO 和 Chassis Plate 為必填！");
        return;
      }

      loading.value = true;

      try {
        const token = localStorage.getItem("authToken");

        const payload = {
          Token: token,
          Timestamp: Date.now(),
          Sign: "string",
          new_trailer: { ...formData.value },
        };

        console.log("🚀 API 傳遞的資料:", payload);

        const response = await axios.post(
          `${API_BASE_URL}/Trailer/Add`,
          payload
        );

        if (response.data.Code === 1000) {
          ElMessage.success("Chassis added successfully!");
          emit("add-success");
          closeDialog();
        } else {
          ElMessage.error("Add failed: " + response.data.Code);
        }
      } catch (error) {
        console.error("API error:", error);
        ElMessage.error("API request failed.");
      } finally {
        loading.value = false;
      }
    };

    /** 🚀 關閉對話框（關閉時清空表單） */
    const closeDialog = () => {
      emit("close");
    };

    return {
      dialogVisible,
      formData,
      loading,
      chassisCorps,
      submitAdd,
      closeDialog,
    };
  },
};
</script>

<style scoped>
.el-dialog {
  border-radius: 8px;
}
</style>
