<script>
import axios from "axios";
import { mapState, mapActions } from "vuex";
import { API_BASE_URL } from "@/config"; // ✅ 這樣才會正確導入變數

export default {
  setup() {
    // 將 'I', 'D', 'E' 轉換為對應的 Load Type 顯示名稱
    const formatOrderType = (row) => {
      const typeMap = {
        I: "Import",
        D: "Domestic",
        E: "Export",
      };
      return typeMap[row.order?.order_type] || "Unknown"; // 預設顯示 "Unknown"
    };

    return {
      formatOrderType,
    };
  },
  data() {
    return {
      orderList: [],
      searchQuery: "",
      loading: false,
      currentPage: 1,
      pageSize: 100,
      editDialogVisible: false,
      selectedOrder: null,
      showFilter: false, // 🔹 控制篩選條件開關
      filter: {
        // 🔹 儲存篩選條件
        scheduleDate: null,
        loadId: "",
        driverUserId: "",
        loadStatus: "",
        booking: "",
        container: "",
        customerLocation: "",
        addressDetail: "",
        loadNotes: "",
        loadNumber: "",
        pickTerminal: "",
        dropTerminal: "",
        vessel: "",
      },
    };
  },
  computed: {
    ...mapState(["authToken"]),

    filteredOrders() {
      if (!this.searchQuery) return this.orderList;
      const query = this.searchQuery.toLowerCase();
      return this.orderList.filter(
        (order) =>
          (order.order.customer_id_name &&
            order.order.customer_id_name.toLowerCase().includes(query)) ||
          (order.order.order_id &&
            order.order.order_id.toString().includes(query))
      );
    },
    paginatedOrders() {
      const start = (this.currentPage - 1) * this.pageSize;
      const end = start + this.pageSize;
      return this.filteredOrders.slice(start, end);
    },
  },
  methods: {
    ...mapActions(["checkTokenExpiration"]),

    async fetchOrders() {
      this.loading = true;
      try {
        let token = localStorage.getItem("authToken");
        if (!token) {
          alert("Session expired, please log in again.");
          this.$router.push("/login");
          return;
        }
        const response = await axios.post(
          `${API_BASE_URL}/Order/ListByDate`,
          {
            Token: token,
            Timestamp: Date.now(),
            Sign: "string",
            start_date: "2024-02-01",
            end_date: "2024-03-30",
          }
        );

        this.orderList =
          response.data.Code === 1000 ? response.data.order_list || [] : [];
      } catch (error) {
        console.error("❌ API Error:", error);
        this.orderList = [];
      } finally {
        this.loading = false;
      }
    },
  },
  mounted() {
    this.fetchOrders();
  },
};
</script>

