<template>
  <el-dialog
    title="Add Truck Group"
    v-model="dialogVisible"
    width="600px"
    @close="closeDialog"
    append-to-body
    :lock-scroll="false"
    :modal-append-to-body="false"
  >
    <el-form ref="formRef" :model="formData" label-width="200px">
      <!-- Truck Group Name -->
      <el-form-item label="* Truck Group Name" required>
        <el-input v-model="formData.truck_group_name" />
      </el-form-item>

      <!-- Truck Group Master ID -->
      <el-form-item label="* Truck Group Master ID" required>
        <el-input v-model="formData.truck_group_master_id" />
      </el-form-item>

      <!-- Truck Group Master PWD -->
      <el-form-item label="* Truck Group Master PWD" required>
        <el-input
          v-model="formData.truck_group_master_pwd"
          type="password"
          autocomplete="new-password"
          show-password
        />
      </el-form-item>

      <!-- User Name -->
      <el-form-item label="* User Name" required>
        <el-input v-model="formData.user_name" />
      </el-form-item>
    </el-form>

    <!-- Dialog Footer -->
    <template #footer>
      <el-button @click="closeDialog">Close</el-button>
      <el-button type="primary" @click="submitAdd" :loading="loading"
        >Save</el-button
      >
    </template>
  </el-dialog>
</template>

<script>
import axios from "axios";
import { ref, computed } from "vue";
import { ElMessage } from "element-plus";
import { API_BASE_URL } from "@/config"; // ✅ 這樣才會正確導入變數

export default {
  props: {
    visible: Boolean, // 控制對話框開關
  },
  setup(props, { emit }) {
    /** 🚀 確保 `dialogVisible` 與 `props.visible` 同步 */
    const dialogVisible = computed({
      get: () => props.visible,
      set: (val) => {
        if (!val) closeDialog();
      },
    });

    const loading = ref(false);

    /** ✅ 初始化表單資料 */
    const formData = ref({
      truck_group_name: "",
      truck_group_master_id: "",
      truck_group_master_pwd: "",
      user_name: "",
    });

    /** 🚀 提交新增 Truck Group */
    const submitAdd = async () => {
      if (
        !formData.value.truck_group_name ||
        !formData.value.truck_group_master_id ||
        !formData.value.truck_group_master_pwd ||
        !formData.value.user_name
      ) {
        ElMessage.error("請填寫所有必填欄位！");
        return;
      }

      loading.value = true;

      try {
        const token = localStorage.getItem("authToken");

        const payload = {
          Token: token,
          Timestamp: Date.now(),
          Sign: "string",
          group_data: { ...formData.value },
        };

        console.log("🚀 API 傳遞的資料:", payload);

        const response = await axios.post(
          `${API_BASE_URL}/Truck/GroupAdd`,
          payload
        );

        if (response.data.Code === 1000) {
          ElMessage.success("Truck Group added successfully!");

          // 🚀 傳遞更新後的數據，確保前台刷新
          emit("add-success", { ...formData.value });

          // 🚀 清空表單
          resetForm();

          closeDialog();
        } else {
          ElMessage.error("Add failed: " + response.data.Code);
        }
      } catch (error) {
        console.error("API error:", error);
        ElMessage.error("API request failed.");
      } finally {
        loading.value = false;
      }
    };

    /** 🚀 清空表單 */
    const resetForm = () => {
      formData.value = {
        truck_group_name: "",
        truck_group_master_id: "",
        truck_group_master_pwd: "",
        user_name: "",
      };
    };

    /** 🚀 關閉對話框 */
    const closeDialog = () => {
      resetForm();
      emit("close");
    };

    return {
      dialogVisible,
      formData,
      loading,
      submitAdd,
      closeDialog,
    };
  },
};
</script>

<style scoped>
.el-dialog {
  border-radius: 8px;
}
</style>
