<template>
  <el-dialog
    title="Edit Truck Driver"
    v-model="dialogVisible"
    width="600px"
    @close="closeDialog"
    append-to-body
    :lock-scroll="false"
    :modal-append-to-body="false"
  >
    <el-form ref="formRef" :model="formData" label-width="150px">
      <!-- Driver User ID（可編輯） -->
      <el-form-item label="Driver User ID">
        <el-input v-model="formData.driver_user_id" />
      </el-form-item>

      <!-- Driver Name -->
      <el-form-item label="Driver Name">
        <el-input v-model="formData.driver_name" />
      </el-form-item>

      <!-- Driver PWD -->
      <el-form-item label="Driver PWD">
        <el-input v-model="formData.driver_pwd" type="password" show-password />
      </el-form-item>

      <!-- Truck Group Name（下拉選單） -->
      <el-form-item label="Truck Group Name">
        <el-select
          v-model="formData.truck_group_id"
          placeholder="Select Truck Group"
        >
          <el-option
            v-for="group in truckGroups"
            :key="group.id"
            :label="group.name"
            :value="group.id"
          />
        </el-select>
      </el-form-item>

      <!-- Driver License -->
      <el-form-item label="Driver License">
        <el-input v-model="formData.driver_license" />
      </el-form-item>

      <!-- License Expiration Date（日期選擇器） -->
      <el-form-item label="License Expiration Date">
        <el-date-picker
          v-model="datePickerValue"
          type="date"
          format="MM/DD/YYYY"
          placeholder="Select Date"
          @change="updateLicenseDate"
        />
      </el-form-item>
    </el-form>

    <!-- Dialog Footer -->
    <template #footer>
      <el-button @click="closeDialog">Close</el-button>
      <el-button type="primary" @click="submitEdit">Save</el-button>
    </template>
  </el-dialog>
</template>

<script>
import axios from "axios";
import { ref, watch } from "vue";
import { ElMessage } from "element-plus";
import dayjs from "dayjs";
import { API_BASE_URL } from "@/config"; // ✅ 這樣才會正確導入變數

export default {
  props: {
    visible: Boolean, // 控制對話框是否開啟
    driverData: Object, // 司機數據
    truckGroups: Array, // Truck Group 名稱與 ID
  },
  setup(props, { emit }) {
    const dialogVisible = ref(props.visible);
    const formData = ref({});
    const datePickerValue = ref(null); // `el-date-picker` 需要 `Date` 類型

    // **監聽 `visible` 來同步 `dialogVisible` 並更新數據**
    watch(
      () => props.visible,
      (newVal) => {
        dialogVisible.value = newVal;
        if (newVal) updateFormData(props.driverData);
      }
    );

    // **監聽 `driverData` 確保載入數據**
    watch(
      () => props.driverData,
      (newVal) => {
        updateFormData(newVal);
      }
    );

    // **初始化數據**
    const updateFormData = (data) => {
      formData.value = {
        ...data,
        _driver_user_id: data.driver_user_id, // API 需要 `_driver_user_id`
        truck_id: data.truck_id || "", // 確保 truck_id 不為 undefined
      };

      // ✅ **直接顯示 API 給的日期**
      if (data.license_expiration_date) {
        let parsedDate = dayjs(data.license_expiration_date); // 直接解析
        datePickerValue.value = parsedDate.isValid()
          ? parsedDate.toDate()
          : null;
      } else {
        datePickerValue.value = null;
      }
    };

    // **更新 license_expiration_date**
    const updateLicenseDate = (date) => {
      if (date) {
        formData.value.license_expiration_date =
          dayjs(date).format("YYYY-MM-DD") + "T00:00:00.000Z";
      }
    };

    // **提交更新**
    const submitEdit = async () => {
      try {
        const token = localStorage.getItem("authToken");

        // **確保 `_driver_user_id` 不變，即便 `driver_user_id` 被修改**
        if (formData.value.driver_user_id !== props.driverData.driver_user_id) {
          formData.value._driver_user_id = props.driverData.driver_user_id;
        }

        // ✅ 確保 `license_expiration_date` 正確轉換
        if (
          formData.value.license_expiration_date &&
          !formData.value.license_expiration_date.includes("T")
        ) {
          formData.value.license_expiration_date =
            dayjs(formData.value.license_expiration_date).format("YYYY-MM-DD") +
            "T00:00:00.000Z";
        }

        const payload = {
          Token: token,
          Timestamp: Date.now(),
          Sign: "string",
          driver_data: {
            driver_user_id: formData.value.driver_user_id, // 新帳號
            driver_name: formData.value.driver_name,
            _driver_user_id: formData.value._driver_user_id, // 保持舊帳號
            driver_pwd: formData.value.driver_pwd
              ? formData.value.driver_pwd
              : "",
            truck_group_id: formData.value.truck_group_id,
            truck_id: formData.value.truck_id, // ✅ 確保 truck_id 正確
            driver_license: formData.value.driver_license,
            license_expiration_date: formData.value.license_expiration_date,
          },
        };

        console.log("🚀 API 傳遞的資料:", payload);

        const response = await axios.post(
          `${API_BASE_URL}/Truck/TruckDriverUpdate`,
          payload
        );

        if (response.data.Code === 1000) {
          ElMessage.success("Driver updated successfully!");

          // ✅ 更新本地 `driverList`
          emit("update-success", { ...formData.value });

          closeDialog();
        } else {
          ElMessage.error("Update failed: " + response.data.Code);
        }
      } catch (error) {
        console.error("API error:", error);
        ElMessage.error("API request failed.");
      }
    };

    // **關閉對話框**
    const closeDialog = () => {
      dialogVisible.value = false;
      emit("close");
    };

    return {
      dialogVisible,
      formData,
      datePickerValue,
      updateLicenseDate,
      submitEdit,
      closeDialog,
    };
  },
};
</script>

<style scoped>
.el-dialog {
  border-radius: 8px;
}
</style>
