<template>
  <el-dialog
    v-model="dialogVisible"
    width="800px"
    :destroy-on-close="true"
    @opened="handleDialogOpened"
    @close="closeDialog"
  >
    <!-- 🔍 搜尋框 (搜尋 Note) -->
    <div class="search-container">
      <el-input
        v-model="searchQuery"
        placeholder="Search in Note..."
        clearable
        class="search-input"
      />
    </div>

    <!-- 🔵 上方分頁 -->
    <el-pagination
      background
      layout="prev, pager, next"
      :total="filteredData.length"
      :page-size="pageSize"
      :current-page="currentPage"
      @current-change="handlePageChange"
      class="pagination-container top-pagination"
    />

    <el-table
      :data="paginatedLogs"
      border
      stripe
      v-loading="loading"
      :key="tableKey"
    >
      <el-table-column
        prop="action"
        label="Action"
        width="200"
      ></el-table-column>
      <el-table-column
        prop="user_id"
        label="User ID"
        width="150"
      ></el-table-column>
      <el-table-column
        prop="str_logdate"
        label="Log Date"
        width="200"
      ></el-table-column>
      <el-table-column prop="note" label="Note"></el-table-column>
    </el-table>

    <!-- 🔵 下方分頁 -->
    <el-pagination
      background
      layout="prev, pager, next"
      :total="filteredData.length"
      :page-size="pageSize"
      :current-page="currentPage"
      @current-change="handlePageChange"
      class="pagination-container bottom-pagination"
    />

    <template #footer>
      <div class="dialog-footer">
        <el-button @click="closeDialog">Close</el-button>
      </div>
    </template>
  </el-dialog>
</template>

<script>
import axios from "axios";
import { API_BASE_URL } from "@/config"; // ✅ 這樣才會正確導入變數

export default {
  props: {
    visible: Boolean,
    userId: String, // 接收用戶 ID
  },
  data() {
    return {
      dialogVisible: false, // 綁定對話框顯示狀態
      logData: [], // 存放 API 返回的 Log 記錄
      loading: false,
      tableKey: 0, // 用來強制 `el-table` 重新渲染
      currentPage: 1, // 當前頁碼
      pageSize: 100, // 每頁顯示筆數
      searchQuery: "", // 🔍 搜尋 Note 的關鍵字
    };
  },
  computed: {
    // **根據搜尋條件過濾資料**
    filteredData() {
      if (!this.searchQuery) {
        return this.logData;
      }
      const query = this.searchQuery.toLowerCase();
      return this.logData.filter(
        (log) => log.note && log.note.toLowerCase().includes(query)
      );
    },
    // **分頁顯示的資料**
    paginatedLogs() {
      const start = (this.currentPage - 1) * this.pageSize;
      const end = start + this.pageSize;
      return this.filteredData.slice(start, end);
    },
  },
  watch: {
    visible(newVal) {
      this.dialogVisible = newVal;
      if (newVal && this.userId) {
        this.fetchLogs(); // 彈窗開啟時載入數據
      }
    },
  },
  methods: {
    async fetchLogs() {
      if (!this.userId) {
        console.error("❌ 錯誤: userId 為空，無法獲取日誌");
        return;
      }
      this.loading = true;
      try {
        let token = localStorage.getItem("authToken");
        if (!token) {
          alert("Your session has expired. Please log in again.");
          this.$router.push("/login");
          return;
        }

        const payload = {
          Token: token,
          Timestamp: Date.now(),
          Sign: "string",
          user_id: this.userId, // 帶入用戶 ID
        };

        console.log("📢 發送 API 請求 /Admin/EmployeeLog:", payload);
        const response = await axios.post(
          `${API_BASE_URL}/Admin/EmployeeLog`,
          payload
        );

        if (response.data.Code === 1000) {
          this.logData = response.data.data || [];
          console.log("✅ API 回應:", this.logData);
          this.currentPage = 1; // 重設為第一頁
          this.searchQuery = ""; // 清空搜尋條件
          this.refreshTable(); // 確保表格刷新
        } else {
          this.$message.error("Failed to fetch logs: " + response.data.Code);
        }
      } catch (error) {
        console.error("❌ API Request Failed:", error);
        this.$message.error("Failed to fetch logs. Please try again later.");
      } finally {
        this.loading = false;
      }
    },
    handleDialogOpened() {
      this.$nextTick(() => {
        window.dispatchEvent(new Event("resize")); // 確保 `el-table` 重新計算尺寸
      });
    },
    refreshTable() {
      this.tableKey += 1; // 強制 `el-table` 重新渲染
    },
    closeDialog() {
      this.$emit("update:visible", false);
    },
    // **處理分頁切換**
    handlePageChange(newPage) {
      this.currentPage = newPage;
    },
  },
};
</script>

<style scoped>
.dialog-footer {
  display: flex;
  justify-content: flex-end;
}

/* 🔍 搜尋框 */
.search-container {
  display: flex;
  justify-content: flex-start;
  margin-bottom: 15px;
}

/* 搜尋輸入框 */
.search-input {
  width: 300px;
}

/* 🔵 分頁按鈕區 */
.pagination-container {
  display: flex;
  justify-content: center;
  margin-top: 10px;
}

/* 🔝 上方分頁 */
.top-pagination {
  margin-bottom: 10px;
}

/* 🔻 下方分頁 */
.bottom-pagination {
  margin-top: 10px;
}
</style>
