<template>
  <div class="rank-page">
    <h1>Rank</h1>

    <!-- 🔍 搜尋區域 -->
    <div class="search-container">
      <el-input
        v-model="searchQuery"
        placeholder="Search Week or Customer Name"
        clearable
        class="search-input"
      />
    </div>

    <!-- 分頁功能（上方） -->
    <el-pagination
      background
      layout="prev, pager, next"
      :total="filteredData.length"
      :page-size="pageSize"
      v-model:current-page="currentPage"
      class="pagination-container top-pagination"
    />

    <!-- 表格 -->
    <el-table
      :data="paginatedData"
      border
      stripe
      style="width: 100%"
      v-loading="loading"
    >
      <el-table-column prop="year" label="Year" width="100"></el-table-column>
      <el-table-column prop="week" label="Week" width="100"></el-table-column>
      <el-table-column prop="customer_name" label="Customer Name" width="250">
        <template #default="{ row }">
          {{ row.customer_name ? row.customer_name : row.user_id }}
        </template>
      </el-table-column>
      <el-table-column
        prop="login_count"
        label="Login Count"
        width="150"
        sortable
        :sort-method="sortByNumber"
      />
    </el-table>

    <!-- 分頁功能（下方） -->
    <el-pagination
      background
      layout="prev, pager, next"
      :total="filteredData.length"
      :page-size="pageSize"
      v-model:current-page="currentPage"
      class="pagination-container bottom-pagination"
    />
  </div>
</template>

<script>
import axios from "axios";
import { mapState, mapActions } from "vuex"; // ✅ 確保 authToken 正確讀取
import { API_BASE_URL } from "@/config"; // ✅ 這樣才會正確導入變數

export default {
  data() {
    return {
      rankData: [], // 全部資料
      searchQuery: "", // 搜尋關鍵字
      loading: false,
      currentPage: 1, // 目前頁數
      pageSize: 20, // 每頁顯示筆數
    };
  },
  computed: {
    ...mapState(["authToken"]),

    // **篩選資料** (支援 Week、Customer Name、User ID)
    filteredData() {
      if (!this.searchQuery) {
        return this.rankData;
      }
      const query = this.searchQuery.toLowerCase();
      return this.rankData.filter(
        (item) =>
          item.week.toString().includes(query) ||
          (item.customer_name &&
            item.customer_name.toLowerCase().includes(query)) ||
          (item.user_id && item.user_id.toLowerCase().includes(query)) // ✅ 加入 user_id 搜尋
      );
    },

    // **分頁資料**
    paginatedData() {
      const start = (this.currentPage - 1) * this.pageSize;
      const end = start + this.pageSize;
      return this.filteredData.slice(start, end);
    },
  },
  methods: {
    ...mapActions(["checkTokenExpiration"]),

    sortByNumber(a, b) {
      return a.login_count - b.login_count;
    },

    async fetchRankData() {
      this.loading = true;
      try {
        let token = this.authToken || localStorage.getItem("authToken");
        if (!token) {
          alert("您的登入已過期，請重新登入。");
          this.$router.push("/login");
          return;
        }

        // 發送 API 請求
        const response = await axios.post(
          `${API_BASE_URL}/Admin/LoginRank`,
          {
            Token: token,
            Timestamp: Date.now(),
            Sign: "string",
          }
        );

        if (response.data.Code === 1000) {
          this.rankData = response.data.data || [];
          this.currentPage = 1; // 確保重新加載時回到第一頁
        } else {
          alert("Failed to fetch rank data: " + response.data.Code);
        }
      } catch (error) {
        console.error("❌ API 請求失敗:", error);
        alert("Failed to fetch rank data. 請稍後重試！");
      } finally {
        this.loading = false;
      }
    },
  },
  mounted() {
    this.fetchRankData();
  },
};
</script>

