<template>
  <el-dialog
    v-model="dialogVisible"
    title="Edit Employee"
    width="600px"
    @close="closeDialog"
  >
    <el-form ref="editForm" :model="formData" label-width="200px">
      <el-form-item label="Employee Name">
        <el-input v-model="formData.employee_name" />
      </el-form-item>

      <el-form-item label="Employee User ID">
        <el-input v-model="formData.employee_user_id" />
      </el-form-item>

      <el-form-item label="Employee User Password">
        <el-input v-model="formData.employee_user_pwd" type="password" />
      </el-form-item>

      <el-form-item label="User Group">
        <el-select
          v-model="formData.user_group"
          multiple
          placeholder="Select User Group"
        >
          <el-option
            v-for="group in userGroups"
            :key="group"
            :label="group"
            :value="group"
          />
        </el-select>
      </el-form-item>
    </el-form>

    <template #footer>
      <div class="dialog-footer">
        <el-button @click="closeDialog">Close</el-button>
        <el-button type="primary" @click="saveEmployee">Save</el-button>
      </div>
    </template>
  </el-dialog>
</template>

<script>
import axios from "axios";
import { API_BASE_URL } from "@/config"; // ✅ 這樣才會正確導入變數

export default {
  props: {
    visible: Boolean, // 控制彈窗顯示
    employeeData: Object, // 員工資料
  },
  data() {
    return {
      dialogVisible: false, // 用於控制對話框顯示
      formData: {
        employee_id: 0,
        employee_name: "",
        employee_user_id: "",
        employee_user_pwd: "",
        user_group: [],
      },
      userGroups: [
        "Administrator",
        "API",
        "EMP",
        "MGMT",
        "MONEY",
        "TCSR",
        "TDRV",
        "TPIC",
      ], // 下拉選單內容
    };
  },
  watch: {
    // 監聽 `visible`，同步 `dialogVisible`
    visible(newVal) {
      this.dialogVisible = newVal;
    },
    // 當 employeeData 更新時，確保每次都是新數據
    employeeData: {
      immediate: true,
      handler(newData) {
        if (newData) {
          this.formData = JSON.parse(
            JSON.stringify({
              employee_id: newData.employee_id,
              employee_name: newData.employee_name,
              employee_user_id: newData.employee_user_id,
              employee_user_pwd: "", // 密碼預設為空，避免顯示
              user_group: newData.employee_group
                ? newData.employee_group.map((g) => g.group_name)
                : [],
            })
          );
        }
      },
    },
  },
  methods: {
    closeDialog() {
      this.$emit("update:visible", false);
    },
    async saveEmployee() {
      try {
        let token = localStorage.getItem("authToken");
        if (!token) {
          alert("Your session has expired. Please log in again.");
          this.$router.push("/login");
          return;
        }

        const payload = {
          Token: token,
          Timestamp: Date.now(),
          Sign: "string",
          data: {
            ...this.formData,
            user_group: Array.isArray(this.formData.user_group)
              ? this.formData.user_group
              : [], // 確保是陣列
          },
        };

        console.log("🔹 正在發送的 payload:", JSON.stringify(payload, null, 2));

        const response = await axios.post(
          `${API_BASE_URL}/Admin/EmployeeEdit`,
          payload
        );

        if (response.data.Code === 1000) {
          this.$message.success("Employee updated successfully!");
          this.$emit("update-success");
          this.closeDialog();
        } else {
          this.$message.error(
            "Failed to update employee: " + response.data.Code
          );
        }
      } catch (error) {
        console.error("❌ API Request Failed:", error);
        this.$message.error(
          "Failed to update employee. Please try again later."
        );
      }
    },
  },
};
</script>

<style scoped>
.dialog-footer {
  display: flex;
  justify-content: flex-end;
}
</style>
