<template>
  <div class="map-container">
    <div class="controls">
      <el-select
        v-model="selectedCompany"
        placeholder="Select Trucking Company"
        @change="filterDrivers"
      >
        <el-option label="All Companies" value="" />
        <el-option
          v-for="company in dropdownMenu"
          :key="company.text"
          :label="company.text"
          :value="company.text"
        />
      </el-select>
      <el-select
        v-model="selectedDriver"
        placeholder="Select Driver"
        @change="filterDrivers"
      >
        <el-option label="All Drivers" value="" />
        <el-option
          v-for="driver in filteredDrivers"
          :key="driver.id"
          :label="driver.text"
          :value="driver.text"
        />
      </el-select>
    </div>

    <GoogleMap
      :center="center"
      :zoom="zoom"
      style="width: 100%; height: 80%"
      :options="{ disableDefaultUI: false, zoomControl: true }"
    >
      <Marker
        v-for="(location, index) in filteredLocations"
        :key="index"
        :options="{ position: { lat: location.lat, lng: location.lng } }"
        @click="selectLocation(location)"
      />

      <InfoWindow
        v-if="selectedLocation"
        :options="{
          position: { lat: selectedLocation.lat, lng: selectedLocation.lng },
        }"
        @closeclick="selectedLocation = null"
      >
        <div>
          <p><strong>Driver:</strong> {{ selectedLocation.text }}</p>
          <p>
            <strong>Coordinates:</strong> {{ selectedLocation.lat }},
            {{ selectedLocation.lng }}
          </p>
        </div>
      </InfoWindow>
    </GoogleMap>
  </div>
</template>

<script>
import axios from "axios";
import { ref, computed, onMounted } from "vue";
import { GoogleMap, Marker, InfoWindow } from "vue3-google-map";
import { API_BASE_URL } from "@/config"; // ✅ 這樣才會正確導入變數

export default {
  components: { GoogleMap, Marker, InfoWindow },
  setup() {
    const locations = ref([]);
    const dropdownMenu = ref([]);
    const center = ref(null);
    const zoom = ref(10);
    const selectedCompany = ref(null);
    const selectedDriver = ref(null);
    const selectedLocation = ref(null);

    /** 監聽 filteredDrivers */
    const filteredDrivers = computed(() => {
      if (!selectedCompany.value) return [];
      const company = dropdownMenu.value.find(
        (c) => c.text === selectedCompany.value
      );
      return company ? company.children : [];
    });

    /** 監聽 filteredLocations，確保當選擇 "All" 時仍顯示所有圖釘 */
    const filteredLocations = computed(() => {
      if (!locations.value.length) return [];

      if (selectedDriver.value) {
        return locations.value.filter(
          (loc) => loc.text === selectedDriver.value
        );
      }

      if (selectedCompany.value) {
        return locations.value.filter((loc) =>
          filteredDrivers.value.some((driver) => driver.text === loc.text)
        );
      }

      // ✅ 若 "All" 被選擇，回傳所有圖釘
      return locations.value;
    });

    const fetchData = async () => {
      try {
        const token = localStorage.getItem("authToken");
        const response = await axios.post(
          `${API_BASE_URL}/Map/DriverOnline`,
          {
            Token: token,
            Timestamp: Date.now(),
            Sign: "string",
          }
        );

        if (response.data.Code === 1000) {
          const mapData = response.data.data;
          locations.value = mapData.locations;
          dropdownMenu.value = mapData.DropdownMenu;
          center.value = mapData.center;
          zoom.value = mapData.zoom;
        } else {
          console.error("API response error", response.data);
        }
      } catch (error) {
        console.error("Failed to fetch map data", error);
      }
    };

    /** 選擇圖釘 */
    const selectLocation = (location) => {
      selectedLocation.value = location;
    };

    /** 變更選擇時，重置數據 */
    const filterDrivers = () => {
      if (!selectedCompany.value) selectedDriver.value = ""; // ✅ 當選擇 "All Companies" 時，清空 selectedDriver
      selectedLocation.value = null;
    };

    onMounted(fetchData);

    return {
      locations,
      dropdownMenu,
      center,
      zoom,
      selectedCompany,
      selectedDriver,
      selectedLocation,
      filteredDrivers,
      filteredLocations,
      selectLocation,
      filterDrivers,
    };
  },
};
</script>

<style scoped>
.map-container {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
}

.controls {
  display: flex;
  justify-content: space-between;
  padding: 10px;
  background: #f8f9fa;
}
</style>
