<template>
  <div class="employee-page">
    <h1>Employee List</h1>

    <!-- 🔍 搜尋區域 -->
    <div class="search-container">
      <el-input
        v-model="searchQuery"
        placeholder="Search Employee Name or User ID"
        clearable
        class="search-input"
      />
    </div>

    <!-- 分頁功能（上方） -->
    <el-pagination
      background
      layout="prev, pager, next"
      :total="filteredData.length"
      :page-size="pageSize"
      v-model:current-page="currentPage"
      class="pagination-container top-pagination"
    />

    <!-- 🔵 Add 按鈕 -->
    <div class="button-container">
      <el-button type="primary" size="small" @click="addDialogVisible = true"
        >Add</el-button
      >
    </div>

    <!-- 表格 -->
    <el-table :data="paginatedData" border stripe v-loading="loading">
      <!-- 🔵 按鈕列 -->
      <el-table-column label="Actions" width="180">
        <template #default="{ row }">
          <el-button type="primary" size="small" @click="handleEdit(row)"
            >Edit</el-button
          >
          <el-button type="primary" size="small" @click="handleLog(row)"
            >Log</el-button
          >
        </template>
      </el-table-column>

      <el-table-column
        prop="employee_name"
        label="Employee Name"
        width="250"
      ></el-table-column>
      <el-table-column
        prop="employee_user_id"
        label="Employee User ID"
        width="200"
      ></el-table-column>

      <!-- ✅ 解析 user_group 陣列 -->
      <el-table-column label="User Group" width="250">
        <template #default="{ row }">
          <span v-if="row.employee_group && row.employee_group.length > 0">
            {{ row.employee_group.map((group) => group.group_name).join(", ") }}
          </span>
          <span v-else>-</span>
        </template>
      </el-table-column>
    </el-table>

    <!-- 分頁功能（下方） -->
    <el-pagination
      background
      layout="prev, pager, next"
      :total="filteredData.length"
      :page-size="pageSize"
      v-model:current-page="currentPage"
      class="pagination-container bottom-pagination"
    />

    <!-- 🔵 Add 員工彈窗 -->
    <AdminAddDialog
      v-model:visible="addDialogVisible"
      @update-success="fetchEmployeeData"
    />

    <!-- 🔵 Edit 員工彈窗 -->
    <AdminEditDialog
      v-model:visible="editDialogVisible"
      :employeeData="selectedEmployee"
      @update-success="fetchEmployeeData"
    />

    <!-- 🔵 Log 員工彈窗 -->
    <AdminLogDialog
      v-model:visible="logDialogVisible"
      :userId="selectedUserId"
    />
  </div>
</template>

<script>
import axios from "axios";
import { mapState, mapActions } from "vuex";
import AdminAddDialog from "@/components/AdminAddDialog.vue"; // 引入新增員工對話框
import AdminEditDialog from "@/components/AdminEditDialog.vue"; // 引入編輯員工對話框
import AdminLogDialog from "@/components/AdminLogDialog.vue"; // 引入 Log 對話框
import { API_BASE_URL } from "@/config"; // ✅ 這樣才會正確導入變數

export default {
  components: {
    AdminAddDialog,
    AdminEditDialog,
    AdminLogDialog,
  },
  data() {
    return {
      employeeData: [],
      searchQuery: "",
      loading: false,
      currentPage: 1,
      pageSize: 20,
      addDialogVisible: false, // 控制新增員工彈窗
      editDialogVisible: false, // 控制編輯員工彈窗
      logDialogVisible: false, // 控制 Log 彈窗
      selectedEmployee: null,
      selectedUserId: null, // 用於查詢 Log 的 User ID
    };
  },
  computed: {
    ...mapState(["authToken"]),

    filteredData() {
      if (!this.searchQuery) return this.employeeData;
      const query = this.searchQuery.toLowerCase();
      return this.employeeData.filter(
        (item) =>
          (item.employee_name &&
            item.employee_name.toLowerCase().includes(query)) ||
          (item.employee_user_id &&
            item.employee_user_id.toLowerCase().includes(query))
      );
    },

    paginatedData() {
      const start = (this.currentPage - 1) * this.pageSize;
      const end = start + this.pageSize;
      return this.filteredData.slice(start, end);
    },
  },
  methods: {
    ...mapActions(["checkTokenExpiration"]),

    async fetchEmployeeData() {
      this.loading = true;
      try {
        let token = this.authToken || localStorage.getItem("authToken");
        if (!token) {
          alert("Your session has expired. Please log in again.");
          this.$router.push("/login");
          return;
        }

        const response = await axios.post(
          `${API_BASE_URL}/Admin/EmployeeList`,
          {
            Token: token,
            Timestamp: Date.now(),
            Sign: "string",
          }
        );

        console.log("✅ API 回應資料:", response.data);

        if (response.data.Code === 1000) {
          this.employeeData = response.data.data || [];
          this.currentPage = 1;
        } else {
          alert("Failed to fetch employee data: " + response.data.Code);
        }
      } catch (error) {
        console.error("❌ API Request Failed:", error);
        alert("Failed to fetch employee data. Please try again later.");
      } finally {
        this.loading = false;
      }
    },

    handleEdit(employee) {
      this.selectedEmployee = JSON.parse(JSON.stringify(employee));
      this.editDialogVisible = true;
    },

    handleLog(employee) {
      this.selectedUserId = employee.employee_user_id;
      this.logDialogVisible = true;
    },
  },
  mounted() {
    this.fetchEmployeeData();
  },
};
</script>

