<template>
  <div class="container">
    <div class="header-container">
      <h2>Chassis Management</h2>
      <!-- 📊 統計表格 -->
      <div class="stats-table">
        <h3>Chassis Statistics</h3>
        <table>
          <thead>
            <tr>
              <th>Category</th>
              <th>Count</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(item, index) in trailerCounts" :key="index">
              <td>{{ item.name }}</td>
              <td>{{ item.count }}</td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>

    <!-- 🔍 搜尋區域 -->
    <div class="search-container">
      <el-input
        v-model="searchQuery"
        placeholder="Search Chassis Plate"
        clearable
      />
    </div>

    <!-- 📌 分頁功能（上方） -->
    <el-pagination
      background
      v-model="currentPage"
      :page-size="50"
      :total="filteredChassis.length"
      layout="prev, pager, next"
      class="pagination-container top-pagination"
      @current-change="handlePageChange"
    />

    <!-- 🔵 Add 按鈕 -->
    <div class="button-container">
      <el-button type="primary" size="small" @click="addChassis">Add</el-button>
    </div>

    <!-- 🚛 Chassis Table -->
    <el-table :data="paginatedChassis" style="width: 100%">
      <el-table-column label="Actions" width="150">
        <template v-slot="{ row }">
          <el-button type="primary" size="small" @click="openEditDialog(row)"
            >Edit</el-button
          >
        </template>
      </el-table-column>

      <el-table-column label="Chassis ID" prop="trailer_id" />
      <el-table-column label="Chassis NO" prop="trailer_no" />
      <el-table-column label="Chassis State">
        <template v-slot="{ row }">
          <span>{{ row.trailer_state === 1 ? "Assigned" : "Unassigned" }}</span>
        </template>
      </el-table-column>
      <el-table-column label="Chassis Plate" prop="trailer_plate" />
      <el-table-column
        label="Chassis Reg Expired At"
        prop="str_trailer_reg_expired_at"
      />
      <el-table-column
        label="Chassis Plate Expired At"
        prop="str_trailer_plate_expired_at"
      />
      <el-table-column
        label="Chassis Check Expired At"
        prop="str_trailer_check_expired_at"
      />
      <el-table-column label="Chassis Corp" prop="trailer_corp" />
      <el-table-column label="Chassis Area" prop="trailer_area" />
    </el-table>

    <!-- 📌 分頁功能（下方） -->
    <el-pagination
      background
      v-model="currentPage"
      :page-size="50"
      :total="filteredChassis.length"
      layout="prev, pager, next"
      class="pagination-container"
      @current-change="handlePageChange"
    />

    <!-- 📝 編輯對話框 -->
    <ChassisEditDialog
      v-if="editDialogVisible"
      :visible="editDialogVisible"
      :chassisData="selectedChassis"
      @update-success="handleUpdateSuccess"
      @close="editDialogVisible = false"
    />

    <!-- ➕ 新增對話框 -->
    <ChassisAddDialog
      v-model="addDialogVisible"
      @add-success="handleAddSuccess"
      @close="addDialogVisible = false"
    />
  </div>
</template>

<script>
import axios from "axios";
import { ref, computed, onMounted } from "vue";
import { ElMessage } from "element-plus";
import ChassisAddDialog from "./ChassisAddDialog.vue";
import ChassisEditDialog from "./ChassisEditDialog.vue";
import { API_BASE_URL } from "@/config"; // ✅ 這樣才會正確導入變數

export default {
  components: { ChassisAddDialog, ChassisEditDialog },
  setup() {
    const chassisList = ref([]); // 🚀 確保不會是 `undefined`
    const trailerCounts = ref([]);
    const searchQuery = ref("");
    const currentPage = ref(1);
    const addDialogVisible = ref(false);
    const editDialogVisible = ref(false);
    const selectedChassis = ref(null);

    /** 🔥 取得 Chassis 列表 */
    const fetchData = async () => {
      try {
        chassisList.value = []; // 先初始化，避免 undefined 問題
        const token = localStorage.getItem("authToken");
        const response = await axios.post(
          `${API_BASE_URL}/Trailer/GroupList`,
          {
            Token: token,
            Timestamp: Date.now(),
            Sign: "string",
            trailer_id: "",
          }
        );

        if (response.data.Code === 1000) {
          chassisList.value =
            response.data.trailer_list?.map((trailer) => ({
              trailer_id: trailer.trailer_id,
              trailer_no: trailer.trailer_no,
              trailer_state: trailer.trailer_state,
              trailer_plate: trailer.trailer_plate || "",
              str_trailer_reg_expired_at:
                trailer.str_trailer_reg_expired_at || "",
              str_trailer_plate_expired_at:
                trailer.str_trailer_plate_expired_at || "",
              str_trailer_check_expired_at:
                trailer.str_trailer_check_expired_at || "",
              trailer_corp: trailer.trailer_corp || "",
              trailer_area: trailer.trailer_area || "",
            })) || [];

          trailerCounts.value = response.data.trailer_Counts || [];
        } else {
          ElMessage.error("Failed to fetch chassis data.");
        }
      } catch (error) {
        console.error("Error fetching chassis list:", error);
        ElMessage.error("API Request Failed.");
      }
    };

    /** 🔄 搜尋功能 */
    const filteredChassis = computed(() => {
      if (!chassisList.value) return []; // 確保是陣列
      if (!searchQuery.value) return chassisList.value;
      return chassisList.value.filter((chassis) =>
        chassis.trailer_plate
          ?.toLowerCase()
          .includes(searchQuery.value.toLowerCase())
      );
    });

    /** ✅ 分頁處理 */
    const paginatedChassis = computed(() =>
      filteredChassis.value.slice(
        (currentPage.value - 1) * 50,
        currentPage.value * 50
      )
    );

    /** ✅ 開啟新增對話框 */
    const addChassis = () => {
      console.log("Opening Add Dialog");
      addDialogVisible.value = true;
    };

    /** ✅ 開啟編輯對話框 */
    const openEditDialog = (chassis) => {
      selectedChassis.value = { ...chassis };
      editDialogVisible.value = true;
    };

    /** ✅ 重新載入數據 */
    const handleUpdateSuccess = () => {
      editDialogVisible.value = false;
      fetchData();
    };

    const handleAddSuccess = () => {
      addDialogVisible.value = false;
      fetchData();
    };

    onMounted(() => {
      fetchData();
    });

    return {
      chassisList,
      trailerCounts,
      searchQuery,
      currentPage,
      addDialogVisible,
      editDialogVisible,
      selectedChassis,
      addChassis,
      openEditDialog,
      filteredChassis,
      paginatedChassis,
      handlePageChange: (page) => (currentPage.value = page),
      handleUpdateSuccess,
      handleAddSuccess,
    };
  },
};
</script>

