  padding: 10px; /* 增加間距 */
  border-bottom: 1px solid #ddd;
}

.pagination-container {
  display: flex;
  justify-content: center;
  margin: 20px 0;
}

/* 讓最後一列的底線消失，避免看起來太擠 */
