<template>
  <el-dialog
    title="Customer Edit"
    v-model="dialogVisible"
    width="600px"
    @close="closeDialog"
    append-to-body
    :lock-scroll="false"
    :modal-append-to-body="false"
  >
    <el-form ref="formRef" :model="formData" label-width="180px">
      <!-- 🏷️ Customer Name -->
      <el-form-item label="Customer Name" required>
        <el-input
          v-model="formData.customer_name"
          placeholder="Enter Customer Name"
        />
      </el-form-item>

      <!-- 🆔 Customer User ID (不可修改) -->
      <el-form-item label="Customer User ID" required>
        <el-input v-model="formData.customer_user_id" disabled />
      </el-form-item>

      <!-- 🔑 Password (可選填，只有輸入時才會更新) -->
      <el-form-item label="Password">
        <el-input
          v-model="formData.customer_user_pwd"
          type="password"
          placeholder="Enter New Password (Leave Blank to Keep Old Password)"
          show-password
        />
      </el-form-item>
    </el-form>

    <!-- 🔘 Dialog Footer -->
    <template #footer>
      <el-button @click="closeDialog">Close</el-button>
      <el-button type="primary" @click="submitEdit" :loading="loading"
        >Save</el-button
      >
    </template>
  </el-dialog>
</template>

<script>
import axios from "axios";
import { ref, computed, watch } from "vue";
import { ElMessage } from "element-plus";
import { API_BASE_URL } from "@/config"; // ✅ 這樣才會正確導入變數

export default {
  props: {
    visible: Boolean, // 控制對話框開關
    customerData: Object, // 從 `CustomerPage.vue` 帶入的客戶資料
  },
  emits: ["update:visible", "update-success"], // 成功後觸發重新整理

  setup(props, { emit }) {
    const dialogVisible = computed({
      get: () => props.visible,
      set: (val) => {
        if (!val) closeDialog();
      },
    });

    const loading = ref(false);
    const authUser = localStorage.getItem("authUser") || "liwang"; // 當前登入的使用者

    // **初始化表單**
    const formData = ref({
      customer_id: "",
      customer_name: "",
      customer_user_id: "",
      customer_user_pwd: "", // 不會自動填充密碼，只有使用者輸入時才會更新
    });

    /** 🚀 監聽 `customerData`，更新 `formData` */
    watch(
      () => props.customerData,
      (newVal) => {
        if (newVal) {
          formData.value = {
            customer_id: newVal.customer_id,
            customer_name: newVal.customer_name,
            customer_user_id: newVal.customer_user_id,
            customer_user_pwd: "", // 保持密碼欄位為空
          };
        }
      },
      { immediate: true }
    );

    /** 🚀 提交更新 Customer */
    const submitEdit = async () => {
      if (!formData.value.customer_name) {
        ElMessage.error("Customer Name is required!");
        return;
      }

      loading.value = true;

      try {
        const token = localStorage.getItem("authToken");

        const payload = {
          Token: token,
          Timestamp: Date.now(),
          Sign: "string",
          customer: {
            customer_id: formData.value.customer_id,
            customer_name: formData.value.customer_name,
            customer_user_id: formData.value.customer_user_id,
            updated_at: Date.now(),
            updated_user_id: authUser,
          },
        };

        // **如果使用者有輸入新密碼，則一起傳送**
        if (formData.value.customer_user_pwd) {
          payload.customer.customer_user_pwd = formData.value.customer_user_pwd;
        }

        console.log("🚀 API 更新傳遞的資料:", payload);

        const response = await axios.post(
          `${API_BASE_URL}/Customer/Update`,
          payload
        );

        if (response.data.Code === 1000) {
          ElMessage.success("Customer updated successfully!");
          emit("update-success"); // 🔄 通知 `CustomerPage.vue` 重新獲取數據
          closeDialog();
        } else {
          ElMessage.error("Update failed: " + response.data.Code);
        }
      } catch (error) {
        console.error("API error:", error);
        ElMessage.error("API request failed.");
      } finally {
        loading.value = false;
      }
    };

    /** 🚀 關閉對話框 */
    const closeDialog = () => {
      emit("close");
    };

    return {
      dialogVisible,
      formData,
      loading,
      submitEdit,
      closeDialog,
    };
  },
};
</script>

<style scoped>
.el-dialog {
  border-radius: 8px;
}
</style>
