import { createRouter, createWebHistory } from "vue-router";
import MainLayout from "@/components/MainLayout.vue";
import LoginPage from "@/components/LoginPage.vue";
import OrderPage from "@/components/OrderPage.vue";
import CustomerPage from "@/components/CustomerPage.vue";
import ChassisPage from "@/components/ChassisPage.vue";
import TruckPage from "@/components/TruckPage.vue";
import DriverFleetPage from "@/components/DriverFleetPage.vue";
import PortPage from "@/components/PortPage.vue";
import TruckDriverPage from "@/components/TruckDriverPage.vue";
import MapPage from "@/components/MapPage.vue";
import AdminPage from "@/components/AdminPage.vue";
import RankPage from "@/components/RankPage.vue";
import BillingPage from "@/components/BillingPage.vue";

// 動態載入頁面，確保結構清晰
const Home = { template: "<div><h1>Home Page</h1></div>" };

const routes = [
  {
    path: "/",
    component: MainLayout,
    children: [
      { path: "login", component: LoginPage },
      { path: "", component: Home },
      { path: "order", component: OrderPage },
      { path: "customer", component: CustomerPage },
      { path: "truck", component: TruckPage },
      { path: "chassis", component: ChassisPage },
      { path: "driver-fleet", component: DriverFleetPage },
      { path: "port", component: PortPage },
      { path: "truck-driver", component: TruckDriverPage },
      { path: "map", component: MapPage },
      { path: "admin", component: AdminPage },
      { path: "rank", component: RankPage },
      { path: "billing", component: BillingPage },
    ],
  },
  { path: "/:pathMatch(.*)*", redirect: "/login" },
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

// **全局守衛：未登入的使用者被導向 `/login`**
router.beforeEach((to, from, next) => {
  const isAuthenticated = localStorage.getItem("authToken");
  if (to.path !== "/login" && !isAuthenticated) {
    next("/login");
  } else {
    next();
  }
});

export default router;
