<template>
  <el-dialog
    title="Edit Truck"
    v-model="dialogVisible"
    width="600px"
    @close="closeDialog"
    append-to-body
    :lock-scroll="false"
    :modal-append-to-body="false"
  >
    <el-form ref="formRef" :model="formData" label-width="200px">
      <!-- 🚚 Truck ID (隱藏但傳遞) -->
      <el-input v-model="formData.truck_id" type="hidden" />

      <!-- Truck Group ID (不可更改) -->
      <el-form-item label="Truck Group ID">
        <el-input v-model="formData.truck_group_id" disabled />
      </el-form-item>

      <!-- Truck Master ID -->
      <el-form-item label="Truck Master ID">
        <el-input v-model="formData.truck_master_id" />
      </el-form-item>

      <!-- Truck Area -->
      <el-form-item label="Truck Area">
        <el-input v-model="formData.truck_area" />
      </el-form-item>

      <!-- Truck Corp -->
      <el-form-item label="Truck Corp">
        <el-input v-model="formData.truck_corp" />
      </el-form-item>

      <!-- Truck Year -->
      <el-form-item label="Truck Year">
        <el-input v-model="formData.truck_year" />
      </el-form-item>

      <!-- Truck Make -->
      <el-form-item label="Truck Make">
        <el-input v-model="formData.truck_make" />
      </el-form-item>

      <!-- Truck Plate -->
      <el-form-item label="Truck Plate">
        <el-input v-model="formData.truck_plate" />
      </el-form-item>

      <!-- Vin Number -->
      <el-form-item label="Vin Number">
        <el-input v-model="formData.vin_number" />
      </el-form-item>

      <!-- Truck State -->
      <el-form-item label="Truck State">
        <el-select v-model="formData.truck_state" placeholder="Select State">
          <el-option label="ACTIVE" :value="1" />
          <el-option label="INACTIVE" :value="0" />
        </el-select>
      </el-form-item>

      <!-- Truck Ins Expired Time (日期選擇器) -->
      <el-form-item label="Truck Ins Expired Time">
        <el-date-picker
          v-model="formData.truck_ins_expired_at_time"
          type="date"
          format="MM-DD-YYYY"
          value-format="YYYY-MM-DDTHH:mm:ss.SSS"
        />
      </el-form-item>

      <!-- Truck Reg Expired Time (日期選擇器) -->
      <el-form-item label="Truck Reg Expired Time">
        <el-date-picker
          v-model="formData.truck_reg_expired_at_time"
          type="date"
          format="MM-DD-YYYY"
          value-format="YYYY-MM-DDTHH:mm:ss.SSS"
        />
      </el-form-item>
    </el-form>

    <!-- Dialog Footer -->
    <template #footer>
      <el-button @click="closeDialog">Close</el-button>
      <el-button type="primary" @click="submitEdit" :loading="loading"
        >Save</el-button
      >
    </template>
  </el-dialog>
</template>

<script>
import axios from "axios";
import { ref, computed, watch } from "vue";
import { ElMessage } from "element-plus";
import { API_BASE_URL } from "@/config"; // ✅ 這樣才會正確導入變數

export default {
  props: {
    visible: Boolean, // 控制對話框開關
    truckData: Object, // 傳入的 Truck 資料
  },
  setup(props, { emit }) {
    const dialogVisible = computed({
      get: () => props.visible,
      set: (val) => {
        if (!val) closeDialog();
      },
    });

    const loading = ref(false);

    // 🔄 初始化表單資料
    const formData = ref({
      truck_id: "",
      truck_group_id: "",
      truck_master_id: "",
      truck_area: "",
      truck_corp: "",
      truck_year: "",
      truck_make: "",
      truck_plate: "",
      vin_number: "",
      truck_state: 1, // 預設 ACTIVE
      truck_ins_expired_at_time: "",
      truck_reg_expired_at_time: "",
    });

    /** 🚀 監聽 `truckData`，更新 `formData` */
    watch(
      () => props.truckData,
      (newVal) => {
        if (newVal) {
          formData.value = {
            truck_id: String(newVal.truck_id || ""),
            truck_group_id: String(newVal.truck_group_id || ""),
            truck_master_id: newVal.truck_master_id || "",
            truck_area: newVal.truck_area || "",
            truck_corp: newVal.truck_corp || "",
            truck_year: newVal.truck_year || "",
            truck_make: newVal.truck_make || "",
            truck_plate: newVal.truck_plate || "",
            vin_number: newVal.vin_number || "",
            truck_state: newVal.truck_state === 1 ? 1 : 0,
            truck_ins_expired_at_time: newVal.truck_ins_expired_at_time
              ? newVal.truck_ins_expired_at_time.split("+")[0] // 去掉 `+08:00`
              : "",
            truck_reg_expired_at_time: newVal.truck_reg_expired_at_time
              ? newVal.truck_reg_expired_at_time.split("+")[0] // 去掉 `+08:00`
              : "",
          };
        }
      },
      { immediate: true }
    );

    /** 🚀 提交更新 Truck */
    const submitEdit = async () => {
      if (!formData.value.truck_id) {
        ElMessage.error("Truck ID 錯誤！");
        return;
      }

      loading.value = true;

      try {
        const token = localStorage.getItem("authToken");

        const payload = {
          Token: token,
          Timestamp: Date.now(),
          Sign: "string",
          truck_data: {
            truck_id: String(formData.value.truck_id),
            truck_group_id: String(formData.value.truck_group_id),
            truck_master_id: formData.value.truck_master_id,
            truck_area: formData.value.truck_area,
            truck_corp: formData.value.truck_corp,
            truck_year: formData.value.truck_year,
            truck_make: formData.value.truck_make,
            truck_plate: formData.value.truck_plate,
            vin_number: formData.value.vin_number,
            truck_state: formData.value.truck_state === 1 ? 1 : 0,
            truck_ins_expired_at_time: formData.value.truck_ins_expired_at_time,
            truck_reg_expired_at_time: formData.value.truck_reg_expired_at_time,
          },
        };

        console.log("🚀 API 更新傳遞的資料:", payload);

        const response = await axios.post(
          `${API_BASE_URL}/Truck/TruckUpdate`,
          payload
        );

        if (response.data.Code === 1000) {
          ElMessage.success("Truck updated successfully!");
          emit("update-success", formData.value);
          closeDialog();
        } else {
          ElMessage.error("Update failed: " + response.data.Code);
        }
      } catch (error) {
        console.error("API error:", error);
        ElMessage.error("API request failed.");
      } finally {
        loading.value = false;
      }
    };

    /** 🚀 關閉對話框 */
    const closeDialog = () => {
      emit("close");
      emit("update-success"); // 通知 TruckPage 重新獲取數據
    };

    return {
      dialogVisible,
      formData,
      loading,
      submitEdit,
      closeDialog,
    };
  },
};
</script>

<style scoped>
.el-dialog {
  border-radius: 8px;
}
</style>
