<template>
  <el-dialog
    title="Add Port"
    v-model="dialogVisible"
    width="600px"
    @close="closeDialog"
    append-to-body
    :lock-scroll="false"
    :modal-append-to-body="false"
  >
    <el-form ref="formRef" :model="formData" label-width="220px">
      <!-- Port Name -->
      <el-form-item label="Port Name" required>
        <el-input v-model="formData.pier_name" />
      </el-form-item>

      <!-- Port Address -->
      <el-form-item label="Port Address" required>
        <el-input v-model="formData.pier_address" />
      </el-form-item>

      <!-- Customer Container Pickup Fee -->
      <el-form-item label="Customer Container Pickup Fee">
        <el-input-number
          v-model="formData.customer_container_pickup_fee"
          :min="0"
        />
      </el-form-item>

      <!-- Driver Container Pickup Fee -->
      <el-form-item label="Driver Container Pickup Fee">
        <el-input-number
          v-model="formData.driver_container_pickup_fee"
          :min="0"
        />
      </el-form-item>

      <!-- Customer Container Dropoff Fee -->
      <el-form-item label="Customer Container Dropoff Fee">
        <el-input-number
          v-model="formData.customer_container_dropoff_fee"
          :min="0"
        />
      </el-form-item>

      <!-- Driver Container Dropoff Fee -->
      <el-form-item label="Driver Container Dropoff Fee">
        <el-input-number
          v-model="formData.driver_container_dropoff_fee"
          :min="0"
        />
      </el-form-item>
    </el-form>

    <!-- Dialog Footer -->
    <template #footer>
      <el-button @click="closeDialog">Close</el-button>
      <el-button type="primary" @click="submitAdd" :loading="loading"
        >Save</el-button
      >
    </template>
  </el-dialog>
</template>

<script>
import axios from "axios";
import { ref, watch } from "vue";
import { ElMessage } from "element-plus";
import { API_BASE_URL } from "@/config"; // ✅ 這樣才會正確導入變數

export default {
  props: {
    visible: Boolean, // 控制對話框開關
  },
  setup(props, { emit }) {
    const dialogVisible = ref(props.visible);
    const loading = ref(false);

    const formData = ref({
      pier_name: "",
      pier_address: "",
      customer_container_pickup_fee: 0,
      driver_container_pickup_fee: 0,
      customer_container_dropoff_fee: 0,
      driver_container_dropoff_fee: 0,
    });

    // 監聽 `visible` 來同步 `dialogVisible` 並重置表單
    watch(
      () => props.visible,
      (newVal) => {
        dialogVisible.value = newVal;
        if (newVal) resetFormData();
      }
    );

    // 重置表單
    const resetFormData = () => {
      formData.value = {
        pier_name: "",
        pier_address: "",
        customer_container_pickup_fee: 0,
        driver_container_pickup_fee: 0,
        customer_container_dropoff_fee: 0,
        driver_container_dropoff_fee: 0,
      };
    };

    // 提交新增
    const submitAdd = async () => {
      if (!formData.value.pier_name || !formData.value.pier_address) {
        ElMessage.error("Port Name 和 Port Address 為必填！");
        return;
      }

      loading.value = true;

      try {
        const token = localStorage.getItem("authToken");

        const payload = {
          Token: token,
          Timestamp: Date.now(),
          Sign: "string",
          pier_data: { ...formData.value },
        };

        console.log("🚀 API 傳遞的資料:", payload);

        const response = await axios.post(
          `${API_BASE_URL}/Customer/PierAdd`,
          payload
        );

        if (response.data.Code === 1000) {
          ElMessage.success("Port added successfully!");
          emit("add-success");
          closeDialog();
        } else {
          ElMessage.error("Add failed: " + response.data.Code);
        }
      } catch (error) {
        console.error("API error:", error);
        ElMessage.error("API request failed.");
      } finally {
        loading.value = false;
      }
    };

    // 關閉對話框
    const closeDialog = () => {
      dialogVisible.value = false;
      emit("close");
    };

    return {
      dialogVisible,
      formData,
      loading,
      submitAdd,
      closeDialog,
    };
  },
};
</script>

<style scoped>
.el-dialog {
  border-radius: 8px;
}
</style>
