<template>
  <el-dialog
    v-model="dialogVisible"
    title="Add Employee"
    width="600px"
    @close="closeDialog"
  >
    <el-form ref="addForm" :model="formData" label-width="200px">
      <el-form-item label="Employee Name">
        <el-input v-model="formData.employee_name" />
      </el-form-item>

      <el-form-item label="Employee User ID">
        <el-input v-model="formData.employee_user_id" />
      </el-form-item>

      <el-form-item label="Employee User Password">
        <el-input v-model="formData.employee_user_pwd" type="password" />
      </el-form-item>

      <el-form-item label="User Group">
        <el-select
          v-model="formData.user_group"
          multiple
          placeholder="Select User Group"
        >
          <el-option
            v-for="group in userGroups"
            :key="group"
            :label="group"
            :value="group"
          />
        </el-select>
      </el-form-item>
    </el-form>

    <template #footer>
      <div class="dialog-footer">
        <el-button @click="closeDialog">Close</el-button>
        <el-button type="primary" @click="addEmployee">Save</el-button>
      </div>
    </template>
  </el-dialog>
</template>

<script>
import axios from "axios";
import { API_BASE_URL } from "@/config"; // ✅ 這樣才會正確導入變數

export default {
  props: {
    visible: Boolean, // 控制對話框顯示
  },
  data() {
    return {
      dialogVisible: false, // 綁定對話框顯示狀態
      formData: {
        employee_name: "",
        employee_user_id: "",
        employee_user_pwd: "",
        user_group: [],
      },
      userGroups: [
        "Administrator",
        "API",
        "EMP",
        "MGMT",
        "MONEY",
        "TCSR",
        "TDRV",
        "TPIC",
      ], // 下拉選單內容
    };
  },
  watch: {
    visible(newVal) {
      this.dialogVisible = newVal;
      if (newVal) {
        this.resetForm();
      }
    },
  },
  methods: {
    async addEmployee() {
      try {
        let token = localStorage.getItem("authToken");
        if (!token) {
          alert("Your session has expired. Please log in again.");
          this.$router.push("/login");
          return;
        }

        const payload = {
          Token: token,
          Timestamp: Date.now(),
          Sign: "string",
          data: this.formData,
        };

        console.log("📢 發送 API 請求 /Admin/EmployeeAdd:", payload);
        const response = await axios.post(
          `${API_BASE_URL}/Admin/EmployeeAdd`,
          payload
        );

        if (response.data.Code === 1000) {
          this.$message.success("Employee added successfully!");
          this.$emit("update-success"); // 通知外部刷新員工列表
          this.closeDialog();
        } else {
          this.$message.error("Failed to add employee: " + response.data.Code);
        }
      } catch (error) {
        console.error("❌ API Request Failed:", error);
        this.$message.error("Failed to add employee. Please try again later.");
      }
    },
    closeDialog() {
      this.$emit("update:visible", false);
    },
    resetForm() {
      this.formData = {
        employee_name: "",
        employee_user_id: "",
        employee_user_pwd: "",
        user_group: [],
      };
    },
  },
};
</script>

<style scoped>
.dialog-footer {
  display: flex;
  justify-content: flex-end;
}
</style>
