<template>
  <el-dialog
    title="Edit Chassis"
    v-model="dialogVisible"
    width="600px"
    @close="closeDialog"
    append-to-body
    :lock-scroll="false"
    :modal-append-to-body="false"
  >
    <el-form ref="formRef" :model="formData" label-width="180px">
      <!-- Chassis ID（不可修改） -->
      <el-form-item label="Chassis ID">
        <el-input v-model="formData.trailer_id" disabled />
      </el-form-item>

      <!-- Chassis NO -->
      <el-form-item label="Chassis NO">
        <el-input v-model="formData.trailer_no" />
      </el-form-item>

      <!-- Chassis State（下拉選單） -->
      <el-form-item label="Chassis State">
        <el-select v-model="formData.trailer_state" placeholder="Select State">
          <el-option label="Assigned" :value="1" />
          <el-option label="Unassigned" :value="0" />
        </el-select>
      </el-form-item>

      <!-- Chassis Plate -->
      <el-form-item label="Chassis Plate">
        <el-input v-model="formData.trailer_plate" />
      </el-form-item>

      <!-- Chassis Reg Expired At -->
      <el-form-item label="Chassis Reg Expired At">
        <el-date-picker
          v-model="formData.trailer_reg_expired_at"
          type="date"
          format="MM-DD-YYYY"
          placeholder="Select Date"
        />
      </el-form-item>

      <!-- Chassis Plate Expired At -->
      <el-form-item label="Chassis Plate Expired At">
        <el-date-picker
          v-model="formData.trailer_plate_expired_at"
          type="date"
          format="MM-DD-YYYY"
          placeholder="Select Date"
        />
      </el-form-item>

      <!-- Chassis Check Expired At -->
      <el-form-item label="Chassis Check Expired At">
        <el-date-picker
          v-model="formData.trailer_check_expired_at"
          type="date"
          format="MM-DD-YYYY"
          placeholder="Select Date"
        />
      </el-form-item>

      <!-- Chassis Corp -->
      <el-form-item label="Chassis Corp">
        <el-select
          v-model="formData.trailer_corp"
          placeholder="Select Chassis Corp"
        >
          <el-option
            v-for="corp in chassisCorps"
            :key="corp"
            :label="corp"
            :value="corp"
          />
        </el-select>
      </el-form-item>

      <!-- Chassis Area -->
      <el-form-item label="Chassis Area">
        <el-input v-model="formData.trailer_area" />
      </el-form-item>
    </el-form>

    <!-- Dialog Footer -->
    <template #footer>
      <el-button @click="closeDialog">Close</el-button>
      <el-button type="primary" @click="submitEdit" :loading="loading"
        >Save</el-button
      >
    </template>
  </el-dialog>
</template>

<script>
import axios from "axios";
import { ref, computed, watch } from "vue";
import { ElMessage } from "element-plus";
import { API_BASE_URL } from "@/config"; // ✅ 這樣才會正確導入變數

export default {
  props: {
    visible: Boolean, // 控制對話框開關
    chassisData: Object, // 從 `ChassisPage.vue` 接收的資料
  },
  emits: ["update:visible", "update-success"], // 更新成功後觸發 reload

  setup(props, { emit }) {
    const dialogVisible = computed({
      get: () => props.visible,
      set: (val) => {
        if (!val) closeDialog();
      },
    });

    const loading = ref(false);

    // 預設空表單
    const defaultFormData = () => ({
      trailer_id: "",
      trailer_no: "",
      trailer_state: 0,
      trailer_plate: "",
      trailer_reg_expired_at: "",
      trailer_plate_expired_at: "",
      trailer_check_expired_at: "",
      trailer_corp: "",
      trailer_area: "",
    });

    const formData = ref(defaultFormData());

    const chassisCorps = ref(["TREND", "TRAC", "PORT", "TOPPOINT", "4JOHN"]);

    /** 🚀 監聽 `chassisData` 變化，載入數據 */
    watch(
      () => props.chassisData,
      (newData) => {
        if (newData) {
          formData.value = { ...newData };
        }
      },
      { immediate: true }
    );

    /** 🚀 提交修改 */
    const submitEdit = async () => {
      if (!formData.value.trailer_no || !formData.value.trailer_plate) {
        ElMessage.error("Chassis NO 和 Chassis Plate 為必填！");
        return;
      }

      loading.value = true;

      try {
        const token = localStorage.getItem("authToken");

        const payload = {
          Token: token,
          Timestamp: Date.now(),
          Sign: "string",
          trailer: {
            trailer_id: formData.value.trailer_id,
            trailer_no: formData.value.trailer_no,
            trailer_state: formData.value.trailer_state === 1 ? 1 : 0, // 確保正確的數值
            trailer_plate: formData.value.trailer_plate,
            trailer_reg_expired_at: formData.value.trailer_reg_expired_at
              ? new Date(formData.value.trailer_reg_expired_at).toISOString()
              : null,
            trailer_plate_expired_at: formData.value.trailer_plate_expired_at
              ? new Date(formData.value.trailer_plate_expired_at).toISOString()
              : null,
            trailer_check_expired_at: formData.value.trailer_check_expired_at
              ? new Date(formData.value.trailer_check_expired_at).toISOString()
              : null,
            trailer_corp: formData.value.trailer_corp,
            trailer_area: formData.value.trailer_area,
          },
        };

        console.log("🚀 API 傳遞的資料:", payload);

        const response = await axios.post(
          `${API_BASE_URL}/Trailer/Edit`,
          payload
        );

        if (response.data.Code === 1000) {
          ElMessage.success("Chassis updated successfully!");
          emit("update-success"); // 通知 `ChassisPage.vue` 重新載入數據
          closeDialog();
        } else {
          ElMessage.error("Edit failed: " + response.data.Code);
        }
      } catch (error) {
        console.error("API error:", error);
        ElMessage.error("API request failed.");
      } finally {
        loading.value = false;
      }
    };

    /** 🚀 關閉對話框 */
    const closeDialog = () => {
      emit("close");
    };

    return {
      dialogVisible,
      formData,
      loading,
      chassisCorps,
      submitEdit,
      closeDialog,
    };
  },
};
</script>

<style scoped>
.el-dialog {
  border-radius: 8px;
}
</style>
