<template>
  <el-dialog
    title="Edit Truck Group"
    v-model="dialogVisible"
    width="600px"
    @close="closeDialog"
    append-to-body
    :lock-scroll="false"
    :modal-append-to-body="false"
  >
    <el-form ref="formRef" :model="formData" label-width="200px">
      <!-- Truck Group Name -->
      <el-form-item label="* Truck Group Name" required>
        <el-input v-model="formData.truck_group_name" />
      </el-form-item>

      <!-- Truck Group Master ID -->
      <el-form-item label="Truck Group Master ID">
        <el-input v-model="formData.truck_group_master_id" />
      </el-form-item>

      <!-- Update User ID (應該清空) -->
      <el-form-item label="Update User ID">
        <el-input v-model="formData.updated_user_id" />
      </el-form-item>

      <!-- User Name -->
      <el-form-item label="User Name">
        <el-input v-model="formData.user_name" />
      </el-form-item>

      <!-- Truck Group Master PWD (防止瀏覽器自動填充) -->
      <el-form-item label="Truck Group Master PWD">
        <el-input
          v-model="formData.truck_group_master_pwd"
          type="password"
          autocomplete="new-password"
          show-password
        />
      </el-form-item>

      <!-- Truck Group State (Dropdown) -->
      <el-form-item label="Truck Group State">
        <el-select
          v-model="formData.truck_group_state"
          placeholder="Select State"
        >
          <el-option label="DRIVING" :value="1" />
          <el-option label="SOLD / NOT DRIVING FOR TOPPOINT" :value="2" />
        </el-select>
      </el-form-item>
    </el-form>

    <!-- Dialog Footer -->
    <template #footer>
      <el-button @click="closeDialog">Close</el-button>
      <el-button type="primary" @click="submitEdit" :loading="loading"
        >Save</el-button
      >
    </template>
  </el-dialog>
</template>

<script>
import axios from "axios";
import { ref, watch, computed } from "vue";
import { ElMessage } from "element-plus";
import { API_BASE_URL } from "@/config"; // ✅ 這樣才會正確導入變數

export default {
  props: {
    visible: Boolean, // 控制對話框開關
    truckGroupData: Object, // Truck Group 資料
  },
  setup(props, { emit }) {
    /** 🚀 確保 `dialogVisible` 與 `props.visible` 同步 */
    const dialogVisible = computed({
      get: () => props.visible,
      set: (val) => {
        if (!val) closeDialog();
      },
    });

    const loading = ref(false);

    /** ✅ 確保 `formData` 能正確載入 `props.truckGroupData` */
    const formData = ref({
      truck_group_id: null,
      truck_group_name: "",
      truck_group_master_id: "",
      truck_group_state: 1,
      updated_user_id: "",
      truck_group_master_pwd: "",
      user_name: "",
    });

    /** 🔄 監聽 `props.truckGroupData`，確保資料載入正確 */
    watch(
      () => props.truckGroupData,
      (newVal) => {
        if (newVal) {
          formData.value = {
            ...newVal,
            // 🚀 如果傳來的值是 "N/A"，則設為空字符串 ""
            truck_group_master_id:
              newVal.truck_group_master_id === "N/A"
                ? ""
                : newVal.truck_group_master_id,
            user_name: newVal.user_name === "N/A" ? "" : newVal.user_name,
            updated_user_id: "", // 🚀 清空 Update User ID
            truck_group_master_pwd: "", // 🚀 確保密碼欄位不帶入預設值
          };
        }
      },
      { immediate: true }
    );

    /** 🚀 提交更新 Truck Group */
    const submitEdit = async () => {
      if (!formData.value.truck_group_name) {
        ElMessage.error("Truck Group Name 為必填！");
        return;
      }

      loading.value = true;

      try {
        const token = localStorage.getItem("authToken");

        const payload = {
          Token: token,
          Timestamp: Date.now(),
          Sign: "string",
          group: { ...formData.value },
        };

        console.log("🚀 API 傳遞的資料:", payload);

        const response = await axios.post(
          `${API_BASE_URL}/Truck/GroupUpdate`,
          payload
        );

        if (response.data.Code === 1000) {
          ElMessage.success("Truck Group updated successfully!");

          // 🚀 傳遞更新後的數據，確保前台刷新
          emit("update-success", { ...formData.value });

          // 🚀 更新成功後清空 `updated_user_id` 和 `truck_group_master_pwd`
          formData.value.updated_user_id = "";
          formData.value.truck_group_master_pwd = "";

          closeDialog();
        } else {
          ElMessage.error("Update failed: " + response.data.Code);
        }
      } catch (error) {
        console.error("API error:", error);
        ElMessage.error("API request failed.");
      } finally {
        loading.value = false;
      }
    };

    /** 🚀 關閉對話框 */
    const closeDialog = () => {
      emit("close");
    };

    return {
      dialogVisible,
      formData,
      loading,
      submitEdit,
      closeDialog,
    };
  },
};
</script>

<style scoped>
.el-dialog {
  border-radius: 8px;
}
</style>
