<template>
  <el-dialog
    title="Customer Add"
    v-model="dialogVisible"
    width="600px"
    @close="closeDialog"
    append-to-body
    :lock-scroll="false"
    :modal-append-to-body="false"
  >
    <el-form ref="formRef" :model="formData" label-width="180px">
      <!-- 🏷️ Customer Name -->
      <el-form-item label="Customer Name" required>
        <el-input
          v-model="formData.customer_name"
          placeholder="Enter Customer Name"
        />
      </el-form-item>

      <!-- 🆔 Customer User ID -->
      <el-form-item label="Customer User ID" required>
        <el-input
          v-model="formData.customer_user_id"
          placeholder="Enter Customer User ID"
          autocomplete="off"
        />
      </el-form-item>

      <!-- 🔑 Password -->
      <el-form-item label="Password" required>
        <el-input
          v-model="formData.customer_user_pwd"
          type="password"
          placeholder="Enter Password"
          show-password
          autocomplete="new-password"
        />
      </el-form-item>
    </el-form>

    <!-- 🔘 Dialog Footer -->
    <template #footer>
      <el-button @click="closeDialog">Close</el-button>
      <el-button type="primary" @click="submitAdd" :loading="loading"
        >Save</el-button
      >
    </template>
  </el-dialog>
</template>

<script>
import axios from "axios";
import { ref, computed } from "vue";
import { ElMessage } from "element-plus";
import { API_BASE_URL } from "@/config"; // ✅ 這樣才會正確導入變數

export default {
  props: {
    visible: Boolean, // 控制對話框開關
  },
  emits: ["update:visible", "add-success"], // 成功後觸發重新整理

  setup(props, { emit }) {
    const dialogVisible = computed({
      get: () => props.visible,
      set: (val) => {
        if (!val) closeDialog();
      },
    });

    const loading = ref(false);

    // **初始化表單**
    const formData = ref({
      customer_name: "",
      customer_user_id: "",
      customer_user_pwd: "",
    });

    /** 🚀 提交新增 Customer */
    const submitAdd = async () => {
      if (
        !formData.value.customer_name ||
        !formData.value.customer_user_id ||
        !formData.value.customer_user_pwd
      ) {
        ElMessage.error("All fields are required!");
        return;
      }

      loading.value = true;

      try {
        const token = localStorage.getItem("authToken");

        const payload = {
          Token: token,
          Timestamp: Date.now(),
          Sign: "string",
          customer: { ...formData.value },
        };

        console.log("🚀 API 傳遞的資料:", payload);

        const response = await axios.post(
          `${API_BASE_URL}/Customer/Add`,
          payload
        );

        if (response.data.Code === 1000) {
          ElMessage.success("Customer added successfully!");
          emit("add-success"); // 🔄 通知 `CustomerPage.vue` 重新獲取數據
          closeDialog();
        } else {
          ElMessage.error("Add failed: " + response.data.Code);
        }
      } catch (error) {
        console.error("API error:", error);
        ElMessage.error("API request failed.");
      } finally {
        loading.value = false;
      }
    };

    /** 🚀 關閉對話框 */
    const closeDialog = () => {
      formData.value = {
        customer_name: "",
        customer_user_id: "",
        customer_user_pwd: "",
      }; // ✅ 清空表單
      emit("close");
    };

    return {
      dialogVisible,
      formData,
      loading,
      submitAdd,
      closeDialog,
    };
  },
};
</script>

<style scoped>
.el-dialog {
  border-radius: 8px;
}
</style>
