<template>
  <div class="navbar" :class="{ collapsed: collapsed }">
    <div class="navbar-left">
      <img src="@/assets/wlogo.svg" alt="TOPPOINT Logo" class="logo-img" />
      <span class="logo-text" v-if="!collapsed">TOPPOINT</span>
    </div>
    <div class="navbar-right">
      <span class="current-time">New Jersey Time: {{ currentTime }}</span>
      <div
        v-if="!isLoginPage && isAuthenticated"
        class="user-menu"
        @click.stop="toggleDropdown"
      >
        <span class="name-text">Hi, {{ username }}</span>
        <div v-if="showDropdown" class="dropdown" ref="dropdownMenu">
          <button @click="handleLogout">Logout</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import dayjs from "dayjs";
import utc from "dayjs/plugin/utc";
import timezone from "dayjs/plugin/timezone";

dayjs.extend(utc);
dayjs.extend(timezone);

export default {
  props: {
    collapsed: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      currentTime: this.getNewJerseyTime(),
      showDropdown: false,
    };
  },
  computed: {
    ...mapGetters(["username", "isAuthenticated"]), // 從 Vuex 獲取 username 和 isAuthenticated
    isLoginPage() {
      return this.$route.path === "/login";
    },
  },
  methods: {
    ...mapActions(["logout"]),
    getNewJerseyTime() {
      return dayjs().tz("America/New_York").format("YYYY/MM/DD hh:mm:ss A");
    },
    toggleDropdown() {
      this.showDropdown = !this.showDropdown;
    },
    async handleLogout() {
      await this.logout(); // 使用 Vuex 的 logout action
      this.$router.push("/login");
      setTimeout(() => {
        window.location.reload(); // 確保登出後重新加載頁面，刷新狀態
      }, 300);
    },
    handleClickOutside(event) {
      if (
        this.$refs.dropdownMenu &&
        !this.$refs.dropdownMenu.contains(event.target)
      ) {
        this.showDropdown = false;
      }
    },
    updateTime() {
      setInterval(() => {
        this.currentTime = this.getNewJerseyTime();
      }, 1000);
    },
  },
  mounted() {
    this.updateTime();
    document.addEventListener("click", this.handleClickOutside);
  },
  beforeUnmount() {
    document.removeEventListener("click", this.handleClickOutside);
  },
};
</script>

