<template>
  <el-dialog
    title="Add Truck Driver"
    v-model="dialogVisible"
    width="600px"
    @close="closeDialog"
    append-to-body
    :lock-scroll="false"
    :modal-append-to-body="false"
  >
    <el-form ref="formRef" :model="formData" label-width="150px">
      <!-- Driver User ID -->
      <el-form-item label="* Driver User ID" required>
        <el-input
          v-model="formData.driver_user_id"
          placeholder="Enter Driver User ID"
        />
      </el-form-item>

      <!-- Driver Name -->
      <el-form-item label="* Driver Name" required>
        <el-input
          v-model="formData.driver_name"
          placeholder="Enter Driver Name"
        />
      </el-form-item>

      <!-- Driver PWD -->
      <el-form-item label="Driver PWD">
        <el-input
          v-model="formData.driver_pwd"
          type="password"
          show-password
          placeholder="Enter Password"
        />
      </el-form-item>

      <!-- Truck Group Name（下拉選單） -->
      <el-form-item label="Truck Group Name">
        <el-select
          v-model="formData.truck_group_id"
          placeholder="Select Truck Group"
        >
          <el-option
            v-for="group in truckGroups"
            :key="group.id"
            :label="group.name"
            :value="group.id"
          />
        </el-select>
      </el-form-item>

      <!-- Driver License -->
      <el-form-item label="Driver License">
        <el-input
          v-model="formData.driver_license"
          placeholder="Enter Driver License"
        />
      </el-form-item>

      <!-- License Expiration Date（日期選擇器） -->
      <el-form-item label="License Expiration Date">
        <el-date-picker
          v-model="datePickerValue"
          type="date"
          format="MM/DD/YYYY"
          placeholder="Select Date"
          @change="updateLicenseDate"
        />
      </el-form-item>
    </el-form>

    <!-- Dialog Footer -->
    <template #footer>
      <el-button @click="closeDialog">Close</el-button>
      <el-button type="primary" @click="submitAdd">Save</el-button>
    </template>
  </el-dialog>
</template>

<script>
import axios from "axios";
import { ref, watch } from "vue";
import { ElMessage } from "element-plus";
import dayjs from "dayjs";
import { API_BASE_URL } from "@/config"; // ✅ 這樣才會正確導入變數

export default {
  props: {
    visible: Boolean, // 控制對話框是否開啟
    truckGroups: Array, // Truck Group 名稱與 ID
  },
  setup(props, { emit }) {
    const dialogVisible = ref(props.visible);
    const formData = ref({});
    const datePickerValue = ref(null); // `el-date-picker` 需要 `Date` 類型

    // **監聽 `visible` 來同步 `dialogVisible` 並清空數據**
    watch(
      () => props.visible,
      (newVal) => {
        dialogVisible.value = newVal;
        if (newVal) resetFormData();
      }
    );

    // **初始化數據**
    const resetFormData = () => {
      formData.value = {
        driver_user_id: "", // ✅ 預設空白
        driver_name: "", // ✅ 預設空白
        driver_pwd: "", // ✅ 預設空白
        truck_group_id: null,
        truck_id: "",
        driver_license: "",
        license_expiration_date: null,
      };
      datePickerValue.value = null;
    };

    // **更新 license_expiration_date**
    const updateLicenseDate = (date) => {
      if (date) {
        formData.value.license_expiration_date = dayjs(date)
          .startOf("day") // 設定時間為 00:00:00
          .toISOString();
      }
    };

    // **提交新增**
    const submitAdd = async () => {
      try {
        if (!formData.value.driver_user_id || !formData.value.driver_name) {
          ElMessage.error("Driver User ID 和 Driver Name 為必填！");
          return;
        }

        const token = localStorage.getItem("authToken");

        const payload = {
          Token: token,
          Timestamp: Date.now(),
          Sign: "string",
          driver_data: {
            driver_user_id: formData.value.driver_user_id,
            driver_name: formData.value.driver_name,
            _driver_user_id: formData.value.driver_user_id, // ✅ 初始時 `_driver_user_id` = `driver_user_id`
            driver_pwd: formData.value.driver_pwd || "", // ✅ 沒填密碼則傳空字串
            truck_group_id: formData.value.truck_group_id,
            truck_id: formData.value.truck_id,
            driver_license: formData.value.driver_license,
            license_expiration_date: formData.value.license_expiration_date, // ✅ 確保格式正確
          },
        };

        const response = await axios.post(
          `${API_BASE_URL}/Truck/TruckDriverAdd`,
          payload
        );

        if (response.data.Code === 1000) {
          ElMessage.success("Driver added successfully!");
          emit("add-success");
          closeDialog();
        } else {
          ElMessage.error("Add failed: " + response.data.Code);
        }
      } catch (error) {
        ElMessage.error("API request failed.");
      }
    };

    // **關閉對話框**
    const closeDialog = () => {
      dialogVisible.value = false;
      emit("close");
    };

    return {
      dialogVisible,
      formData,
      datePickerValue,
      updateLicenseDate,
      submitAdd,
      closeDialog,
    };
  },
};
</script>

<style scoped>
.el-dialog {
  border-radius: 8px;
}
</style>
